
/**
 * 将一个断续的进度通知方法转进行补间并通知另一个连续的补间结果
 * @template {({percentage: number}) => {}} T
 * @param {T} onProgress 连续的方法
 * @param {number} speed 步进的速度
 * @param {number} timeout 每一个补间的间距时间
 * @param {number} initStatus 初始化的进度
 * @param {number} endSpeed 当进度已经到达100的时候加速步进的速度
 * @returns {T}
 */
export default function progressTween(onProgress, speed, timeout = 1000, initStatus, endSpeed) {
  let interval;
  let oldLoadingStatus = initStatus;
  let newLoadingStatus;
  let startTime;
  return (currentLoadingStatus) => {
    if (interval) {
      oldLoadingStatus = newLoadingStatus;
      newLoadingStatus = currentLoadingStatus;
      startTime = Date.now();
      clearInterval(interval);
      onProgress({
        ...oldLoadingStatus,
        percentage: oldLoadingStatus.percentage,
      });
    } else {
      newLoadingStatus = currentLoadingStatus;
      startTime = Date.now();
      onProgress({
        ...newLoadingStatus,
        percentage: newLoadingStatus.percentage,
      });
    }

    interval = setInterval(() => {
      const currentTime = Date.now();
      let end = (currentTime - startTime)
        * (newLoadingStatus.percentage === 100 ? endSpeed : speed)
        + oldLoadingStatus.percentage;
      if (newLoadingStatus.percentage < end) {
        end = newLoadingStatus.percentage;
        clearInterval(interval);
      }
      onProgress({
        ...newLoadingStatus,
        percentage: end,
      });
    }, timeout);
  };
}
