/* eslint-disable class-methods-use-this */

import {
  Color,
  PaperScope as _PaperScope,
  HitResult as _HitResult,
  ToolEvent as _ToolEvent,
  Segment as _Segment,
  KeyEvent as _KeyEvent,
  Path as _Path,
} from 'paper';
import { O } from 'ts-toolbelt';
import BaseTool from './BaseTool';

type HitResult = InstanceType<typeof _HitResult>;
type PagerScope = InstanceType<typeof _PaperScope>;
type ColorInterface = InstanceType<typeof Color>;
type Segment = InstanceType<typeof _Segment>;
type ToolEvent = InstanceType<typeof _ToolEvent>;
type KeyEvent = InstanceType<typeof _KeyEvent>;
type Path = InstanceType<typeof _Path>;


function handleInSameLine(segment: Segment) {
  return !(+(segment.handleIn.angle - segment.handleOut.angle).toFixed(2) % 180);
}

const hitOptions = {
  segments: true,
  stroke: true,
  fill: true,
  tolerance: 5,
  handles: true,
};

type Params = {
  strokeColor ?: ColorInterface | null
  strokeWidth ?: number
  fillColor ?: ColorInterface | null,
  strokeCap ?: string,
};

const defaultParams = {
  strokeColor: new Color('black'),
  strokeWidth: 4,
  strokeCap: 'round',
  fillColor: null,
};

type ComputedParams = O.Required<Params>;

export class Bezier extends BaseTool {
  mouseDownHitResult ?: HitResult | null;
  mouseDownEvent ?: ToolEvent;
  params: ComputedParams;

  get drawingPath() {
    const { path } = this;
    if (path && !path.closed) {
      return path;
    }
    return null;
  }

  constructor(scope: PagerScope, params?: Params) {
    super(scope);
    this.params = { ...defaultParams, ...params };
    this.on('mousedown', (event: ToolEvent) => {
      // @ts-ignore
      const originE = event.event as MouseEvent;
      if (originE.button) return;
      let dragstartData: {
        event: ToolEvent,
        hitResult: HitResult | null
      } | null = null;
      const hitResult = this.project.hitTest(event.point, hitOptions);
      const downData = {
        event,
        hitResult,
      };
      if (!hitResult) {
        this.onBgMouseDown(event);
      // @ts-ignore
      } else if (hitResult.item.guide) {
        //
      } else {
        this.onItemMouseDown(event, downData);
      }
      const dragHandler = (_event: ToolEvent) => {
        if (!dragstartData) {
          const _hitResult = this.project.hitTest(event.point, hitOptions);
          dragstartData = {
            event: _event,
            hitResult: _hitResult,
          };
        }
        // const hitResult = this.project.hitTest(event.point, hitOptions);
        // this.hitResult = hitResult;
        if (!dragstartData.hitResult) {
          // this.onBgMouseDrag(event);
        // @ts-ignore
        } else if (dragstartData.hitResult.item.guide) {
          //
        } else {
          this.onItemMouseDragStart(_event, {
            hitResult: dragstartData.hitResult,
          });
          this.onItemMouseDrag(_event, downData, {
            hitResult: dragstartData.hitResult,
          });
        }
      };
      const upHandler = (_event: ToolEvent) => {
        if (!dragstartData) {
          if (downData.hitResult) {
            this.onItemClick(_event, { hitResult: downData.hitResult });
          } else {
            this.onBgClick(_event);
          }
          // @ts-ignore
        } else if (dragstartData.hitResult && !dragstartData.hitResult.item.guide) {
          this.onItemMouseDragEnd(_event, downData, {
            hitResult: dragstartData.hitResult,
            event: dragstartData.event,
          });
        }
        this.off('mousedrag', dragHandler);
        this.off('mouseup', upHandler);
      };
      this.on('mousedrag', dragHandler);
      this.on('mouseup', upHandler);
    });

    const upHandler = (_event: KeyEvent) => {
      const { path } = this;
      if (path) {
        if (_event.key === 'escape') {
          path.selected = false;
          path.fullySelected = false;
          return;
        }
        if (_event.key === 'delete' || _event.key === 'backspace') {
          if (path.fullySelected) {
            path.remove();
          } else {
            path.segments.forEach(segment => {
              if (segment.selected) {
                segment.remove();
              }
            });
          }
        }
      }
    };
    this.on('keyup', upHandler);
  }

  selectSegment({ event, hitResult } : { event: ToolEvent, hitResult: HitResult & { item: Path } }) {
    const { item } = hitResult;
    if (event.modifiers.control || event.modifiers.command) {
      if (!event.modifiers.shift) {
        item.segments.forEach(segment => {
          segment.selected = false;
        });
      }
      hitResult.segment.selected = !hitResult.segment.selected;
    }
  }

  onBgClick(event: ToolEvent) {}

  onItemClick(
    event: ToolEvent,
    downData: { hitResult: HitResult },
  ) {
    const { Path } = this.scope;
    const hitResult = downData.hitResult!;
    const { item } = hitResult;
    if (item instanceof Path) {
      this.selectSegment({ event, hitResult: hitResult as HitResult & { item: Path } });
    }
  }

  onItemMouseDragStart(
    event: ToolEvent,
    downData: { hitResult: HitResult },
  ) {
    const { Path } = this.scope;
    const hitResult = downData.hitResult!;
    const { item } = hitResult;
    if (item instanceof Path) {
      if (!hitResult.segment.selected) {
        this.selectSegment({ event, hitResult: hitResult as HitResult & { item: Path } });
      }
    }
  }

  onItemMouseDragEnd(event: ToolEvent,
    downData: { hitResult: HitResult | null },
    dragstartData: { hitResult: HitResult }) {}

  onItemMouseDrag(event: ToolEvent,
    downData: { hitResult: HitResult | null },
    dragstartData: { hitResult: HitResult }) {
    const { Path, Point } = this.scope;
    const hitResult = dragstartData.hitResult!;
    const { type, item } = hitResult;
    if (item instanceof Path) {
      if (type === 'segment') {
        if (event.modifiers.control || event.modifiers.command) {
          item.segments.forEach(segment => {
            if (segment.selected) {
              segment.point = new Point(
                segment.point.x + event.delta.x,
                segment.point.y + event.delta.y,
              );
            }
          });
          return;
        }
        this.dragHandler(hitResult.segment, event);
      } else if (type === 'handle-in' || type === 'handle-out') {
        const isHandleIn = type === 'handle-in';
        const key = isHandleIn ? 'handleIn' : 'handleOut';
        const otherKey = isHandleIn ? 'handleOut' : 'handleIn';
        const { segment } = hitResult;
        const isHandleInSameLine = handleInSameLine(segment);
        const point = event.point.subtract(segment.point);
        segment[key] = point;
        const isBreak = event.modifiers.alt || event.modifiers.option;
        if (!isBreak && isHandleInSameLine) {
          const otherPoint = new Point({
            angle: point.angle + 180,
            length: segment[otherKey].length,
          });
          segment[otherKey] = otherPoint;
          // segment.smooth();
        }
      } else if (type === 'stroke') {
        console.log(item);
      }
    }
  }

  dragHandler(segment: Segment, event: ToolEvent) {
    const { Point } = this.scope;
    const point = event.point.subtract(segment.point);
    const otherPoint = new Point({
      angle: point.angle + 180,
      length: point.length,
    });
    segment.selected = true;
    segment.handleOut = point;
    segment.handleIn = otherPoint;
  }

  onBgMouseDown(event: ToolEvent) {
    const { Path } = this.scope;
    let { drawingPath: path } = this;
    const { strokeWidth, strokeColor, strokeCap } = this.params;
    if (path) {
      const _path = path;
      _path.segments.forEach(_segment => {
        _segment.selected = false;
      });
      _path.lineTo(event.point);
    } else {
      this.layer.children.forEach(child => {
        child.selected = false;
      });
      path = new Path();
      path.strokeColor = strokeColor;
      path.strokeWidth = strokeWidth;
      path.strokeCap = strokeCap;
      path.moveTo(event.point);
    }
    path.selected = true;
  }


  onItemMouseDown(event: ToolEvent, extraData: { hitResult: HitResult | null }) {
    const { Path } = this.scope;
    const hitResult = extraData.hitResult!;
    const { item } = hitResult;
    const { drawingPath: path } = this;
    if (path && item !== path) {
      this.onBgMouseDown(event);
      return;
    }
    if (item instanceof Path) {
      if (!item.selected) {
        this.layer.children.forEach(child => {
          child.selected = false;
        });
        item.selected = true;
        item.fullySelected = true;
        return;
      }
      if (hitResult.type === 'segment') {
        if (event.modifiers.control || event.modifiers.command) {
          return;
        }

        if (event.modifiers.alt || event.modifiers.option) {
          hitResult.segment.clearHandles();
          return;
        }

        if (hitResult.segment.isFirst() && this.drawingPath === item) {
          item.closePath();
          // this.fullySelectPath(item);
          item.fullySelected = true;
          return;
        }

        if ((!hitResult.segment.isLast() && this.drawingPath === item) || this.drawingPath !== item) {
          hitResult.segment.remove();
          return;
        }
        // hitResult.segment.selected = true;
      }

      if (hitResult.type === 'stroke') {
        if (event.modifiers.control || event.modifiers.command) {
          return;
        }
        const { location } = hitResult;
        item.curves[location.index].divideAt(location);
        const segment = item.segments[location.index + 1];
        item.segments.forEach(_segment => {
          _segment.selected = false;
        });
        segment.selected = true;
        this.mouseDownHitResult = {
          ...hitResult,
          type: 'segment',
          segment,
        };
      }
    }
  }
}

export default Bezier;
