<template>
  <div class="board-container">
    <!-- navigation -->
    <template v-if="modules && currentModule">
      <nav class="board-nav">
        <template v-for="(item, name) in modules">
          <div
            v-if="item"
            :key="item.id"
            :class="{ current: currentModule === name }"
            class="board-nav-item"
            @dragenter="currentModule = name"
            @click="currentModule = name"
          >
            {{ item.name }}
          </div>
        </template>
      </nav>
      <!-- Relative Component -->
      <LibarayList
        class="libaray-list"
        :key="currentModule"
        v-if="currentModule === 'Library' && $can('system', 'OP0159')"
      />
      <CurrentDesign v-else-if="currentModule === 'CurrentDesign'" />
      <QuarterRecommendationLibrary v-else-if="currentModule === 'QuarterRecommendationLibrary'" />
      <keep-alive v-else>
        <CreateBoard class="create-board" :key="currentModule" :catalog="modules[currentModule]" ref="createBoard" />
      </keep-alive>
    </template>
  </div>
</template>

<script>
import i18n from 'i18next';

import can from '@/utils/can';

import { getCurrentCompany } from '@/loaders/company';

import {

  getPanelBoardList,
} from '@/api/catalogue';
import { getContextUser } from '@/loaders/context';
import LibarayList from './LibraryList';
import CreateBoard from './CreateBoard';
import CurrentDesign from './CurrentDesign';
import QuarterRecommendationLibrary from './QuarterRecommendationLibrary';


export default {
  name: 'board',
  props: {
    role: String,
  },
  components: {
    CreateBoard,
    LibarayList,
    CurrentDesign,
    QuarterRecommendationLibrary,
  },
  data() {
    this._cacheCatalogs = {};
    this._materialCache = {};
    return {
      /**
       * @type {undefined | string}
       */
      currentModule: undefined,
      /** @type {{[x: string]: {
        id: string,
        entryAppType: number,
        appType: number,
        workspaceId: string,
        name: string,
      }}}  */
      modules: undefined,
      listSelect: [], // 活动弹框数据
    };
  },
  created() {
    this.$watch(() => this.groupAppId, (groupAppId) => {
      return this.getNavList(groupAppId);
    }, {
      immediate: true,
    });
  },
  computed: {
    /** @return {string} */
    relateId() { return this.$route.params.modelId; },
    /** @return {string} */
    groupAppId() {
      return this.$route.query.workspaceId;
    },
    /** @return {object} */
    company() {
      return getCurrentCompany();
    },
    /** @return {object} */
    user() {
      return getContextUser();
    },
    /** @return {boolean} */
    isIndependentDesigner() {
      return this.user.mail === 'designer_helper@163.com';
    },
  },
  methods: {

    async getNavList(groupAppId) {
      const {
        createBoardCatalog,
        // materialLibraryCatalog,
        workSpaceCatalog,
      } = (await getPanelBoardList(groupAppId)).data;
      const materialLibraryCatalog = can('system', 'OP0159') && this.company
        && (this.company.type === 5 || this.company.type === 1);
      const currentDesignCatalog = {};
      const modules = {
        CurrentDesign: currentDesignCatalog,
        'Personal Board': createBoardCatalog ?? null,
        Workspace: workSpaceCatalog ?? null,
        QuarterRecommendationLibrary: false,
        Library: materialLibraryCatalog ?? null,
      };

      if (this.isIndependentDesigner) {
        modules['Personal Board'] = false;
        modules.Workspace = false;
      }

      // 如果当前账号是安踏设计师账号
      if (this.user.businessType === 3) {
        // modules['Personal Board'] = false;
        // modules.Workspace = false;
        // modules.Library = false;
        modules.QuarterRecommendationLibrary = true;
      }

      const moduleKeys = Object.keys(modules).filter((key) => !!modules[key]);

      moduleKeys.forEach(key => {
        const module = {
          ...modules[key], time: Date.now(), folderCount: Infinity, fileType: 0,
        };
        if (key === 'CurrentDesign') {
          Object.assign(module, {
            name: i18n.t('model.detail.board.design'),
          });
        }
        if (key === 'Personal Board') {
          Object.assign(module, {
            entryAppType: 12,
            appType: 17,
            workspaceId: '',
            name: i18n.t('model.detail.board.create_board'),
          });
        }
        if (key === 'Workspace') {
          Object.assign(module, {
            entryAppType: 13,
            workspaceId: this.groupAppId,
            name: i18n.t('model.detail.board.workspace'),
          });
        }
        if (key === 'Library') {
          Object.assign(module, {
            name: i18n.t('model.detail.board.library'),
          });
        }
        if (key === 'QuarterRecommendationLibrary') {
          Object.assign(module, {
            name: i18n.t('model.detail.board.quarter_recommendation_library'),
          });
        }
        modules[key] = module;
      });


      // eslint-disable-next-line prefer-destructuring
      this.currentModule = moduleKeys[0];
      this.modules = modules;
    },
  },
};
</script>

<style lang="scss" scoped>
.board-container {
  height: 100%;
  position: relative;
  display: flex;
  flex: 1;
  flex-direction: column;
  min-height: 700px;
  user-select: none;
}

.board-nav {
  display: flex;
  justify-content: space-evenly;
  font-size: 16px;
  padding: 9px 20px 0;
  .board-nav-item {
    text-align: center;
    line-height: 34px;
    border-bottom: 2px solid transparent;
    color: #bbb;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.5s;
    &:first-child {
      margin-left: 0;
    }
    &.current {
      color: $color-primary;
      border-color: $color-primary;
    }
  }
}
</style>
