













































import {
  defineComponent, watch, ref, computed,
} from '@vue/composition-api';

import { i18n } from '@/i18n';
import axios from '@/utils/axios';
import useClosest from '_/composables/use-closest';
import { Paging } from '@/models/Paging';
import { callbackWhenLastElementShown } from '@/utils/composables/use-callback-when-last-element-shown';

import SearchAndSelect, { Tree } from '@/views/file-list/components/catalog/Search&Select.vue';
import PriceForm from '@/views/file-list/components/catalog/PriceForm.vue';
import draggingData from '../../../../utils/dragging-data';
import CollectList from './CollectList.vue';

interface Filters {
  keyword?: string
  recommendSeason: string
  recommendYear: string
  recommendBrand: string
  filters?: {
    materialClassifications?: number[]
    prices?: {
      endPrice: number
      startPrice: number
      unit: number
    }[]
  }
}

interface CostPrice {
  // 成本价单位
  /* eslint-disable camelcase */
  cost_unit: number
  // 成本价价格
  cost_value: number | null
  // 是否能删除,0不能，1能
  delete_status: 0 | 1
  // 参考价单位
  reference_price_unit: number
  // 参考价价格
  reference_price_value: number
  // 供应商id
  supplier_name: string
  // 是否是默认供应商 0不是，1是
  supplier_status: 0 | 1
}

interface RecommendListResp {
  partUsed: string
  name: string
  id: string
  businessType: number
  deleteStatus: number
  extraProperty: {
    [key: string]: any
  }
  imgJson: {
    thumbnail: string
    diffuseMap: string
    flat: string
    specularMap: string
    normalMap: string
    glossinessMap: string
  }
  costPrice: CostPrice
}

interface RecommendList {
  partUsed: string
  name: string
  id: string
  costPrice: CostPrice
  status: number
  businessType: number
  // publishStatus: number
  [key: string]: any
}

const seasonsOption = [
  { label: '春季', value: '1' },
  { label: '夏季', value: '2' },
  { label: '秋季', value: '3' },
  { label: '冬季', value: '4' },
];
const brandsOption = [
  { label: '篮球', value: '11' },
  { label: '跑鞋', value: '55' },
  { label: '综训', value: '77' },
  { label: '生活', value: '80' },
];

export default defineComponent({
  components: {
    SearchAndSelect,
    CollectList,
  },
  setup(props, { root }) {
    const modelClosest = useClosest('model');

    // 当前页码
    let pageNumber = 1;
    // 总数据
    const totalSize = ref(0);
    // 是否没有更多数据
    const isNomore = ref(false);
    // 是否loading
    const isLoading = ref(false);
    // 面料列表
    const materials = ref<RecommendList[]>([]);
    // 面料选择
    const materialSearchOptions = ref<Filters>({
      keyword: '',
      recommendSeason: '',
      recommendYear: '',
      recommendBrand: '',
      filters: {
      },
    });
    const hasRecommendParams = computed(() => modelClosest.value?.model.modelNumber.length >= 7
      && materialSearchOptions.value.recommendYear
      && materialSearchOptions.value.recommendSeason
      && materialSearchOptions.value.recommendBrand);
    // 展示模式
    const displayMode = ref('drape');
    // 申请材料跳转
    const goToApply = () => {
      if (!hasRecommendParams.value) {
        root.$alert(root.$t('model.detail.board.recommend_error'));
        return;
      }
      window.open(`/askfor-material?${modelClosest.value?.workspaceId ? `workspaceId=${modelClosest.value?.workspaceId}&` : ''}`
        + `year=${materialSearchOptions.value.recommendYear}&`
        + `season=${materialSearchOptions.value.recommendSeason}&`
        + `series=${materialSearchOptions.value.recommendBrand}`);
    };
    // 搜索
    const handleSearch = (v: Filters) => {
      materialSearchOptions.value = {
        ...materialSearchOptions.value,
        ...v,
      };
      pageNumber = 1;
    };
    // 分析安踏名字
    // 1120春季篮球22
    const getParamsByAnta = (name: string) => {
      const params = {
        /** 品牌 */
        brand: name.substring(0, 1),
        /** 类型 */
        type: name.substring(1, 2),
        /** 年份 */
        year: `20${name.substring(2, 4)}`,
        /** 季度 */
        season: seasonsOption.find(item => item.value === name.substring(4, 5))?.label ?? '',
        /** 系列 */
        series: brandsOption.find(item => item.value === name.substring(5, 7))?.label ?? '',
        /** 流水号 */
        flow: name.substring(7, 9),
      };
      return params;
    };

    const antaParmas = getParamsByAnta(modelClosest.value?.model.modelNumber);
    materialSearchOptions.value = {
      ...materialSearchOptions.value,
      recommendSeason: antaParmas.season,
      recommendYear: antaParmas.year,
      recommendBrand: antaParmas.series,
    };

    // 获取材料推荐列表
    const getMaterialList = async () => {
      if (!hasRecommendParams.value) {
        root.$alert(root.$t('model.detail.board.recommend_error'));
        return;
      }
      if (isLoading.value === true) {
        return;
      }
      if (pageNumber === 1) {
        materials.value = [];
      }
      // totalSize比列表数小
      if (materials.value.length
        && totalSize.value <= materials.value.length) {
        isNomore.value = true;
        return;
      }

      try {
        isNomore.value = false;
        isLoading.value = true;
        const length = materialSearchOptions.value?.filters?.materialClassifications?.length ?? 0;
        const res = (await axios.post<Paging<RecommendListResp>>('/anta-material/recommend/season/list', {
          pageSize: 30,
          pageNumber,
          searchCondition: materialSearchOptions.value?.keyword ? [materialSearchOptions.value?.keyword] : [],
          categoryId: length ? materialSearchOptions.value.filters?.materialClassifications?.[length - 1] : undefined,
          attachInfo: {
            recommendSeason: materialSearchOptions.value.recommendSeason,
            recommendYear: materialSearchOptions.value.recommendYear,
            recommendBrand: materialSearchOptions.value.recommendBrand,
            price: {
              start: materialSearchOptions.value?.filters?.prices?.[0]?.startPrice,
              end: materialSearchOptions.value?.filters?.prices?.[0]?.endPrice,
              unit: materialSearchOptions.value?.filters?.prices?.[0]?.unit ?? 1,
            },
          },
        })).data;

        totalSize.value = res.total;
        // 请求的数据是空数组
        if (res.records.length === 0) return;
        pageNumber += 1;
        materials.value.push(...(res.records).map(item => {
          return {
            ...item,
            supplier_price_obj: item.costPrice,
            status: item.extraProperty?.material_status,
            businessType: 1,
            ...item.extraProperty,
            // publishStatus: item.extraProperty?.publishStatus,
            ...item.imgJson,
          };
        }));
      } catch (error) {
        root.$errHandler(error);
      } finally {
        isLoading.value = false;
      }
    };
    // 刷新
    const refresh = () => {
      pageNumber = 1;
      getMaterialList();
    };
    // 获取搜索options
    const getSearchChildren = async (node: Tree, options: any, level: number) => {
      if (!node) {
        if (options) {
          return options;
        }
        const children: Tree[] = [{
          code: 'materialClassifications',
          value: 'materialClassifications',
          label: root.$t('model.detail.board.material_type'),
          async getChildren(_, _level) {
            const _node = this;
            const params = {
              requestType: '3',
              ...(_level > 1 ? { parentId: _node.value } : {}),
            };
            return (await axios.post<
              {
                itemCode: string,
                itemValue: string,
                label: string,
                subCount: number | null,
              }[]
            >('bff-center/search/dict/conditions/detail', params, {
              headers: {
                language: i18n.language,
              },
            })).data.map(item => {
              return {
                value: item.itemValue,
                code: item.itemCode,
                label: item.label,
                leaf: !item.subCount,
              };
            });
          },
        }, {
          code: 'prices',
          value: { startPrice: undefined, endPrice: undefined, unit: 1 },
          isSelected(value) {
            return typeof value === 'object' && ('startPrice' in value || 'endPrice' in value) && 'unit' in value;
          },
          label: root.$t('model.detail.board.price_range'),
          slot: PriceForm,
        }];

        return children;
      }
      return node.children ?? [];
    };
    // 处理拖拽
    const handleMaterialDrag = (e: any, data: any) => {
      draggingData.set({ type: 'material', data });
    };
    // 处理选择
    const handleMaterialPick = (data: any) => {
      // eslint-disable-next-line no-unused-expressions
      modelClosest?.value?.updateMaterial(data);
    };

    watch(() => materialSearchOptions.value, () => {
      getMaterialList();
    }, {
      immediate: true,
    });

    callbackWhenLastElementShown(materials, getMaterialList, '.item');

    return {
      goToApply,
      handleSearch,
      refresh,
      materialSearchOptions,
      getSearchChildren,
      handleMaterialDrag,
      handleMaterialPick,
      displayMode,
      materials,
      isLoading,
      isNomore,
    };
  },
});
