<template>
  <div style="height: 346px;">
    <!-- 面料分类列表 -->
    <Scrollable y style="height: 280px;" outsideScrollbar>
      <div v-if="!list.length" class="flex-center flex-column" style="height: 100%">
        <img width="90" height="82" src="@/assets/img/model/empty-hiddenlist.png">
        <span style="padding-top: 24px">
          {{ $t('model.detail.board.no_hidden_folder') }}
        </span>

      </div>
      <el-checkbox-group v-model="listChecked" @change="handleCheckedChange">
        <el-checkbox
          v-for="item in list"
          :label="item"
          :key="item.id"
          style="display: block; line-height: 30px;">
          {{ item.name }}
        </el-checkbox>
      </el-checkbox-group>
    </Scrollable>

    <!-- 提交按钮 -->
    <div class="flex-space-between" style="margin-top: 30px; ">
      <div>
        <el-checkbox
          v-model="isCheckAll"
          :indeterminate="isIndeterminate"
          @change="handleCheckAllChange">
          {{$t('model.detail.board.all')}}({{ listChecked.length }})
          </el-checkbox>
      </div>
      <div style="">
        <el-button size="medium" @click="$emit('cancel')">
          {{ $t('public.dialog.cancel') }}
        </el-button>
        <el-button type="primary" size="medium" @click="confirm">
          {{ $t('public.dialog.confirm') }}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Scrollable from '@/components/Scrollable';

export default {
  dialogify: {
    props: {
      width: '450px',
    },
  },
  components: {
    Scrollable,
  },
  props: {
    // 列表数据
    list: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      visible: false,
      listChecked: [],
      isCheckAll: false,
      isIndeterminate: false,
    };
  },
  methods: {
    // 点击全选时，根据boolean 判断是否是全选状态
    handleCheckAllChange(val) {
      this.listChecked = val ? this.list : [];
      this.isIndeterminate = false;
    },
    // 选择按钮切换时，
    handleCheckedChange(value) {
      const checkedCount = value.length;
      this.isCheckAll = checkedCount === this.list.length;
      // eslint-disable-next-line
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.list.length;
    },
    // 弹框关闭时，初始化上次操作内容
    handleDialogClose() {
      this.listChecked = [];
      this.isCheckAll = false;
      this.isIndeterminate = false;
    },
    // 确认移动
    confirm() {
      // 当有选中项时，才通知父组件
      if (this.listChecked.length) {
        this.$emit('confirm', this.listChecked.map(item => item.id));
      }
      this.visible = false;
    },
  },
};
</script>
