<template>
  <section class="comment-list-container">
    <!-- 列表不为空 -->
    <!-- <el-scrollbar> -->
    <div class="list-wrapper scrollable-y" v-if="!commentList ||commentList.length !== 0"
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="disabled"
      infinite-scroll-immediate="false" ref="comment_list"
      v-loading="!commentList">
      <CommentListItem v-for="comment of commentList"
        :key="comment.id" :info="comment"
        v-bind="showVersionAndReadonly"
        @on-edit-comment="fetchCommentList()"
        @on-delete-comment="handleDeleteCommentItem"
        :atableMembers="members" />
    </div>
    <!-- 当列表为空 -->
    <div class="no-comment-container" v-else>
      <slot name="empty">
        <img src="@/assets/img/renderer/no-comment.png" alt="no-comment">
        <div class="no-comment">
          {{ $t('model.comment.no_comment') }}
        </div>
      </slot>
    </div>
    <!-- </el-scrollbar> -->

  </section>
</template>

<script>
import omitUndefined from '_/omit-undefined';
import {
  fetchCommentList,
  updateDot,
} from '@/api/comment';
import remove from '_/remove';
import CommentListItem from './CommentListItem';

export default {
  name: 'commentList',
  props: {
    objectId: String,
    workspaceId: String,
    readonly: Boolean,
    showVersion: Boolean,
    personal: Boolean,
    members: Array,
  },
  components: {
    CommentListItem,
  },
  data() {
    return {
      loading: false,
      total: undefined,
      cursor: 1,
      current: 1,
      size: undefined,
      commentList: null,
      records: undefined,
      totalPage: undefined,
    };
  },
  methods: {
    handleDeleteCommentItem(item) {
      remove(this.commentList, item);
    },
    async fetchCommentList(type) {
      const { data } = await fetchCommentList(this.workspaceId, {
        personal: this.personal,
        objectId: this.objectId,
      });
      this.commentList = data.records;
      if (type === 'add')
      // scroll to top
      // eslint-disable-next-line
      {
        this.$refs.comment_list && (this.$refs.comment_list.scrollTop = 0);
        // 然后更新红点
        this.updateDotWrapper();
      }
      this.current = 1;
      this.cursor = 1;
    },
    async updateDotWrapper() {
      await updateDot({
        objectId: this.objectId,
      });
    },
    async loadMore() {
      this.loading = true;
      // this.current++;
      const {
        total, size, current, records,
      } = (await fetchCommentList(this.workspaceId, {
        objectId: this.objectId,
        cursor: ++this.cursor,
      })).data;
      if (this.commentList.length === 0) {
        this.commentList = records;
      } else {
        this.commentList.push(...records);
      }
      this.total = total;
      this.totalPage = Math.ceil(this.total / this.limit);
      this.size = size;
      this.current = current;
      this.loading = false;
    },
  },
  computed: {
    showVersionAndReadonly() {
      return omitUndefined({
        showVersion: this.showVersion,
        readonly: this.readonly,
      });
    },
    disabled() {
      return this.loading || this.noMore;
    },
    noMore() {
      return this.totalPage !== undefined && this.current >= this.totalPage;
    },
  },
  async created() {
    await Promise.all([this.fetchCommentList(), this.updateDotWrapper()]);
  },
};
</script>

<style lang="scss" scoped>
.comment-list-container {
  display: flex;
  flex-direction: column;
  flex: 1;
  position: relative;
}
.list-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
}
.no-comment-container {
  flex: 1;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  .icon-comment-null {
    font-size: 5rem;
  }
  .no-comment {
    margin-top: 1rem;
    color: #7f7f7f;
  }
  img {
    opacity: 0.8;
  }
}
</style>
