<template lang="html">
  <div class="record-container scrollable-y">
    <!-- 版本回复文字 -->
    <div v-if="version.sourceCode">
      <h2 class="operation-type"><i class="iconfont icon-restore" />
        {{ $t('model.detail.revert_to_original') }}
      </h2>
      <p class="operation-detail" v-html="$t('model.detail.version_restore_info', { version })">
      </p>
    </div>
    <!-- 版本变化的文字 -->
    <ul style="line-height: 2;">
        <li v-for="(item, i) in version.records"  :key="i">
          <h2 v-if="item.records.length" class="operation-type">
            <i class="iconfont" :class="generateIconByIndex(i)" />
            {{ item.operationName }}
          </h2>
          <ul v-for="(operation, j) in item.records" :key="j">
            <li v-html="operation" class="operation-detail" />
          </ul>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    version: {
      type: Object,
      required: true,
    },
  },
  methods: {
    generateIconByIndex(idx) {
      if (idx === 0) return 'icon-material-print';
      if (idx === 1) return 'icon-material-o';
      if (idx === 2) return 'icon-material-change';
      return 'icon-material-print';
    },
  },
};
</script>

