<template>
  <ul class="save-button-group flex-space-between" v-loading="isLoading"
    :disabled="!isAvailableWithPermission">
    <el-tooltip :content="$t('model.detail.operation.save')">
      <li @click="!disabled && saveShoe('save')" class="iconfont iconsave-nol icon-btn"></li>
    </el-tooltip>
    <el-tooltip :content="$t('model.detail.operation.save_as')">
      <li @click="!disabled && handleSaveAs()" class="iconfont iconsaveas-nol1 icon-btn"></li>
    </el-tooltip>
  </ul>
</template>

<script>
import SaveAsConfirm from './SaveAsConfirm';

export default {
  props: ['modelNumber', 'disabled'],
  data() {
    return {
      isLoading: false,
    };
  },
  computed: {
    /** @return { Vue | undefined } */
    model() {
      return this.$closest('model');
    },
    /** @return { boolean } */
    isAvailableWithPermission() {
      if (this.$route.query.workspaceId) {
        return this.$can('workspace', 'OP0014', false);
      }
      return true;
    },
  },
  methods: {
    async handleSaveAs() {
      const modelNumber = await this.$Dialog.dialogify({
        is: SaveAsConfirm,
        props: {
          modelNumber: this.modelNumber,
        },
      });
      if (modelNumber) this.saveShoe('save as', modelNumber);
    },
    async saveShoe(type, modelNumber) {
      try {
        this.isLoading = true;
        await this.model.save(type, modelNumber);
      } catch (e) {
        this.$errHandler(e);
      } finally {
        this.isLoading = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.iconfont {
  font-size: 24px;
  cursor: pointer;
  &:hover {
    color: $color-primary;
  }
}
.save-button-group {
  width: 70px;
  margin-right: 30px;
}
</style>
