<template>
  <div>
    <div class="dialog-wrapper">
      <!-- 侧边栏版本列表 -->
      <aside class="dialog-aside scrollable-y">
        <version-list :versions="list" @change-status="handleToggleActiveVersion" />
      </aside>

      <!-- 右侧显示版本详情 -->
      <main class="dialog-content">
        <h2 class="content-title">
          {{ $t('model.detail.design_version') }}
        </h2>

        <!-- 版本模型预览图 -->
        <div class="preview-box">
          <base-image fit="contain" :src="version.contentThumbnail" class="shoe-preview" />
          <el-button
            v-if="list.length"
            :disabled="list[0].activeStatus"
            :type="list[0].activeStatus ? 'normal' : 'primary'"
            size="small"
            @click="viewHistoryModel">
            {{ $t('model.detail.view_version') }}
          </el-button>
        </div>

        <!-- 操作记录 -->
        <operation-record-list :version="version" style="margin-bottom: 30px" />

        <!-- 回复按钮 -->
        <button
          v-if="list.length"
          class="recover-version premission-wrapper"
          :class="{ disabled: list[0].activeStatus || !$can('workspace', 'OP0055') }"
          :disabled="!$can('workspace', 'OP0055') || list[0].activeStatus"
          @click="$act(openDialogRestore)">
          <i class="iconfont icon-restore" />
          {{ $t('model.detail.restore') }}
        </button>
      </main>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import BaseImage from '@/components/base/Image';

import { openSingleton } from '@/utils/open-window';
import { getHost } from '@/utils/exec';

import {
  getVersionList,
  getVersionDetail,
  applyVersionToNewest,
} from '@/api/shoe';
import { getUsersInfoByIds } from '@/api/account';
import OperationRecordList from './OperationRecordList';
import VersionList from './VersionList';

export default {
  dialogify: {
    props: {
      compact: true,
      // showClose: false,
      modal: false,
      width: '700px',
    },
  },
  components: {
    BaseImage,
    VersionList,
    OperationRecordList,
  },
  async created() {
    this._initialData();

    this.host = await getHost();
  },
  data() {
    return {
      list: [],
      version: {
        name: '',
        contentThumbnail: '',
        modifiedContentBrief: [],
        modifier: {
          name: '',
        },
        sourceCode: '',
        host: null,
      },
    };
  },
  computed: {
    /** @return { string | (string | null)[] } */
    workspaceId() {
      return this.$route.query.workspaceId;
    },
    /** @return { string | (string | null)[] } */
    catalogId() {
      return this.$route.query.catalogId;
    },
    /** @return { string | (string | null)[] } */
    relateType() {
      return this.$route.query.relateType;
    },
  },
  methods: {
    // 切换不同的版本
    async handleToggleActiveVersion(versionObj) {
      // 点击当前活跃的版本，不做任何操作
      if (!versionObj.activeStatus) {
        // 切换活跃状态
        this.list.forEach(item => {
          item.activeStatus = false;
        });
        versionObj.activeStatus = true;

        // 请求切换的版本的数据
        this.version = await this._getVersionDetailInfo('', versionObj.id, this.workspaceId);

        // 将更改的信息保存为可读性的文字
        // eslint
        this.version.records = this._generateOperationsRecords(this.version.modifiedContentBrief);
      }
    },
    // 浏览历史版本
    async viewHistoryModel() {
      if (this.host) {
        // 查看历史版本
        this.$emit('confirm', {
          catalogId: this.catalogId, workspaceId: this.workspaceId, ...this.version, history: true,
        });
        return;
      }
      console.log('afei=========2222222', this.relateType);
      await openSingleton({
        name: 'ModelPreview',
        params: {
          modelId: this.version.relateId,
        },
        query: {
          workspaceId: this.workspaceId,
          catalogId: this.catalogId,
          relateType: this.relateType,
        },
      }, true, 'ModelPreview');
    },
    // 初始化历史版本的数据
    async _initialData() {
      const { catalogId } = this.$route.query;

      // 获取版本列表
      const result = await getVersionList(catalogId);
      // 为 active 状态，每个数据添加一个属性
      result.data.forEach((item, i) => {
        item.activeStatus = i <= 0;
      });

      // 分别去获取用户信息
      const usersInfo = (await getUsersInfoByIds(result.data.map(item => item.modifierId))).data;
      // 将用户信息放入对应的userId中
      result.data.forEach((item, idx) => {
        item.userInfo = usersInfo[idx];
      });

      this.list = result.data;

      this.version = await this._getVersionDetailInfo(catalogId, '', this.workspaceId);
      // 将更改的信息保存为可读性的文字
      this.version.records = this._generateOperationsRecords(this.version.modifiedContentBrief);
    },
    // 回复旧版本到最新的版本
    async openDialogRestore() {
      // eslint-disable-next-line
      await this.$confirm(i18n.t('model.detail.restore_caution', { vCode: this.version.vCode || this.version.name }), i18n.t('model.detail.process_of_recovery'), {
        confirmButtonText: i18n.t('public.dialog.confirm'),
        cancelButtonText: i18n.t('public.dialog.cancel'),
      });
      const { data: newVersion } = await applyVersionToNewest(this.version.id, this.workspaceId);
      this.$emit('confirm', { catalogId: this.catalogId, workspaceId: this.workspaceId, ...newVersion });
    },
    // 根据不同版本修改的信息，生成对应的字段
    _generateOperationsRecords(arr) {
      const newArr = [
        { operationName: i18n.t('model.detail.material_print_modifications'), records: [] },
        { operationName: i18n.t('model.detail.base_material_modifications'), records: [] },
        { operationName: i18n.t('model.detail.material_modifications'), records: [] },
        { operationName: i18n.t('model.detail.env_setting'), records: [] },
        { operationName: i18n.t('model.detail.camera_setting'), records: [] },
      ];
      arr.forEach(item => {
        const type = item.property;
        const { component } = item;

        // 材质修改
        if (type === 'material_name') {
          // eslint-disable-next-line
          newArr[2].records.push(i18n.t('model.detail.material_name_change', {
            component,
            item,
          }));
        }

        // 材质操作
        if (type === 'material_pan') {
          // eslint-disable-next-line
          newArr[1].records.push(i18n.t('model.detail.material_pan_change', {
            component,
          }));
        }
        if (type === 'material_rotate') {
          // eslint-disable-next-line
          newArr[1].records.push(i18n.t('model.detail.material_rotate_change', {
            component,
          }));
        }

        // 印花操作
        if (type === 'print_map') {
          if (item.oldValue && !item.newValue) {
            // eslint-disable-next-line
            newArr[0].records.push(i18n.t('model.detail.print_map_remove', { component }));
          } else if (item.oldValue) {
            // eslint-disable-next-line
            newArr[0].records.push(i18n.t('model.detail.print_map_replace', { component }))
          } else {
            // eslint-disable-next-line
            newArr[0].records.push(i18n.t('model.detail.print_map_add', { component }));
          }
        }
        // eslint-disable-next-line
        if (type === 'print_pan') newArr[0].records.push(i18n.t('model.detail.print_pan_change', {
          component,
        }));


        // eslint-disable-next-line
        if (type === 'print_rotate') newArr[0].records.push(i18n.t('model.detail.print_rotate', { component }));
        // eslint-disable-next-line
        if (type === 'print_scale') newArr[0].records.push(i18n.t('model.detail.print_scale', { component }));

        // eslint-disable-next-line
        if (type === 'print_wrap') newArr[0].records.push(i18n.t('model.detail.print_wrap', { component }));
        // eslint-disable-next-line
        if (type === 'print_mirrorH') newArr[0].records.push(i18n.t('model.detail.print_mirrorH', { component }));
        // eslint-disable-next-line
        if (type === 'print_mirrorV') newArr[0].records.push(i18n.t('model.detail.print_mirrorV', { component }));

        // newArr[0].records.push('unkonw changed.');
        if (type === 'environmentSetting') {
          // console.log('environmentSetting============', )
          /*
          {
              "component": "environmentSetting",
              "newValue": {
                  "envLightIntensity": 321,
                  "envName": "RenderLight",
                  "lightGroupSettingChange": false,
                  "otherEnvSetting": [
                      {
                          "IBLRotationDegree": 200.00003051757812,
                          "envLightIntensity": 20000,
                          "envName": "LakeZurich"
                      },
                      {
                          "IBLRotationDegree": 200.00003051757812,
                          "envLightIntensity": 123,
                          "envName": "LightBooth"
                      }
                  ],
                  "showShadow": true,
                  "showSkybox": false
              },
              "oldValue": {
                  "envLightIntensity": 123,
                  "envName": "LightBooth",
                  "showShadow": true,
                  "showSkybox": false
              },
              "property": "environmentSetting"
          }
          */
          if (item.oldValue.envName !== item.newValue.envName || item.oldValue.envLightIntensity !== item.newValue.envLightIntensity) {
            if (item.oldValue.envName !== item.newValue.envName) {
              // eslint-disable-next-line
              newArr[3].records.push(i18n.t('model.detail.env_name_change',
                { oldName: item.oldValue.envName, newName: item.newValue.envName, light: item.newValue.envLightIntensity }));
            } else {
              newArr[3].records.push(i18n.t('model.detail.env_light_change', { light: item.newValue.envLightIntensity }));
            }
          }
          if (item.newValue.otherEnvSetting && item.newValue.otherEnvSetting.length > 0) {
            item.newValue.otherEnvSetting.forEach(setting => {
              setting.envLightIntensity && newArr[3].records.push(i18n.t('model.detail.other_env_light_change',
                { envName: setting.envName, light: setting.envLightIntensity }));
              setting.IBLRotationDegree && newArr[3].records.push(i18n.t('model.detail.other_env_ibl_degree_change',
                { envName: setting.envName }));
            });
          }
          if (item.newValue.lightGroupSettingChange) {
            newArr[3].records.push(i18n.t('model.detail.lighting_change'));
          }
          if (item.newValue.showSkybox !== item.oldValue.showSkybox
            || item.newValue.backgroundColor !== item.oldValue.backgroundColor) {
            newArr[3].records.push(i18n.t('model.detail.sky_box_change'));
          }
          if (item.newValue.showShadow !== item.oldValue.showShadow) {
            if (item.newValue.showShadow) {
              newArr[3].records.push(i18n.t('model.detail.show_shadow_on'));
            } else {
              newArr[3].records.push(i18n.t('model.detail.show_shadow_off'));
            }
          }
          if (item.newValue.cameraMode !== item.oldValue.cameraMode) {
            const cameraModeNames = {
              0: i18n.t('model.detail.camera_mode_transparency'),
              1: i18n.t('model.detail.camera_mode_orthographic'),
            };
            newArr[4].records.push(i18n.t('model.detail.camera_mode_change', [
              cameraModeNames[item.oldValue.cameraMode],
              cameraModeNames[item.newValue.cameraMode],
            ]));
          }
        }
      });

      return newArr;
    },
    async _getVersionDetailInfo(...args) {
      const version = (await getVersionDetail(...args)).data;
      // 分别去获取用户信息
      const usersInfo = (await getUsersInfoByIds([version.modifierId])).data[0];
      // 将用户信息放入对应的userId中
      version.userInfo = usersInfo;
      return version;
    },
  },
};
</script>

<style lang="scss" scoped>
