  display: flex;
  position: relative;

  .dialog-aside {
    width: 180px;
    max-height: 600px;
    background-color: #f7f7f7;
  }
  .dialog-content {
    flex: 1;
    max-height: 600px;
    padding: 20px 30px;
  }
}

.content-title {
  font-size: 18px;
  color: #333;
}
.preview-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 20px 0;

  .shoe-preview {
    width: 300px;
    height: 200px;
  }
}

.premission-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 20px;
  right: 30px;
  z-index: 2;
  width: 110px;
  height: 40px;
  font-size: 14px;
  color: #848b97;
  border: none;
  border-radius: 4px;
  outline: none;
  overflow: hidden;
  cursor: pointer;
  background-color: transparent;

  &.disabled {
    cursor: not-allowed;
    color: #ddd;
    background-color: transparent;
  }
  &:hover {
    background-color: #eee;
  }
  &.disabled:hover {
    background-color: transparent;
  }

  .iconfont {
    margin-right: 5px;
    font-size: 20px;
  }
}
</style>
