<template>
  <div class="material-list" @contextmenu.stop="(e) => handleContextmenu(e, undefined, true)">
    <header class="header">
      <div class="brief">
        <slot name="brief"></slot>
      </div>
      <div class="view-tools">
        <DisplayModeSwitcher :value="displayMode" @input="(v) => $emit('update:displayMode', v)" />
      </div>
    </header>
    <div class="loading" v-loading="true" v-if="loadingPage === 1"></div>
    <div class="empty" v-else-if="!materialCatalogs || !materialCatalogs.length">
      <slot name="empty">
        <el-divider key="divider">{{ $t("material-mall.no_more") }}</el-divider>
      </slot>
    </div>

    <Scrollable y v-else class="list-wrapper" outsideScrollbar>
      <transition-group name="list" tag="ul" class="list" @click.stop>
        <li
          v-for="(catalog, idx) in materialCatalogs"
          :key="catalog.id"
          class="item"
          draggable="true"
          @dragstart="handleListDragStart($event)"
        >
          <material-card
            @pick="() => $emit('pick', catalog)"
            @contextmenu="handleContextmenu"
            :selected="selected.includes(catalog)"
            v-on="
              selectable
                ? {
                    'update:selected': (selected) => {
                      toggleSelected(catalog, selected);
                    },
                  }
                : {}
            "
            @drag="clearAndSelect(catalog)"
            :display-mode="displayMode"
            :material="catalog.fileInfo"
            @show-info="handleShoWDialogInfo"
            @magnify="showMagnifyMode(idx)"
          />
        </li>
      </transition-group>
      <Intersect @enter="loadMore" v-if="canLoadMore">
        <div class="load-more">
          <template v-if="loadingPage && loadingPage > 1">
            <i class="el-icon-loading" /> {{ $t("material-mall.loading") }}
          </template>
          <template v-else>
            {{ $t("material-mall.scroll_to_load") }}
          </template>
        </div>
      </Intersect>
      <el-divider v-else>{{ $t("material-mall.no_more") }}</el-divider>
    </Scrollable>
  </div>
</template>

<script>
import { openSingleton } from '@/utils/open-window';

import MaterialInfo from '@/views/material/info';
import AntaMaterialInfo from '@/views/material/info-anta/index.vue';

import Scrollable from '@/components/Scrollable';
import Intersect from 'vue-intersect';
import {
  fetchChildrenWithPaging,
} from '@/loaders/catalog';
import remove from '_/remove';
import toggleFrom from '_/toggle-from';
import MaterialCard from './MaterialCard';
import DisplayModeSwitcher from './DisplayModeSwitcher';
// import { getMaterialInfo } from '@/api/material';
const pageSize = 30;

export default {
  components: {
    MaterialCard,
    Scrollable,
    Intersect,
    DisplayModeSwitcher,
  },
  props: {
    catalog: {
    },
    rootCatalog: {},
    displayMode: {
      type: String,
      default: 'drape',
    },
    selected: {
      type: Array,
      default: () => [],
    },
    searchParams: {},
    fetchMethod: {
      type: Function,
      default: fetchChildrenWithPaging,
    },
  },
  data() {
    return {
      timestamp: Date.now(),
      /** @type {any} */
      materialCatalogs: null,
      loadingPage: undefined,
      catalogPaging: undefined,
    };
  },
  computed: {
    /** @return { any } */
    params() {
      return {
        timestamp: this.timestamp,
        catalog: this.catalog,
        rootCatalog: this.rootCatalog,
        searchParams: this.searchParams,
      };
    },
    /** @return { boolean } */
    selectable() {
      return !!this.$listeners.contextmenu;
    },
    /** @return { boolean } */
    canLoadMore() {
      const { catalogPaging } = this;
      if (!catalogPaging) {
        return false;
      }
      return catalogPaging.pages > catalogPaging.current;
    },
    /** @return { any } */
    materials() {
      if (this.materialCatalogs === this.$loadingState) return this.$loadingState;
      if (!this.materialCatalogs) return this.materialCatalogs;
      return this.materialCatalogs.map(item => (item.fileInfo));
    },
  },
  created() {
    this.$watch(() => {
      return JSON.stringify(this.params);
    }, async (p) => {
      const _params = JSON.parse(p);
      this.loadPage(_params, 1);
    });
    this.$watch('catalogPaging', (paging) => {
      if (paging) {
        if (paging.current === 1) {
          this.materialCatalogs = paging.records;
        } else {
          this.materialCatalogs.splice(this.materialCatalogs.length, 0, ...paging.records);
        }
      } else {
        this.materialCatalogs = null;
      }
    });
  },
  methods: {
    loadMore() {
      if (!this.loadingPage && this.catalogPaging) {
        this.loadPage(this.params, this.catalogPaging.current + 1);
      }
    },
    async loadPage(p, page) {
      const isSearch = p.searchParams;
      const { catalog } = p;
      const root = p.rootCatalog;
      const id = isSearch ? root.id : catalog?.id;
      if (!id) {
        this.catalogPaging = null;
        return;
      }

      this.loadingPage = page;
      const catalogOptions = {
        id,
        relateType: [1],
        searchParams: p.searchParams,
      };
      const pagingOptions = { cursor: page, limit: pageSize };
      this.catalogPaging = await this.fetchMethod(
        catalogOptions,
        pagingOptions,
      );
      this.loadingPage = undefined;
    },
    async remove(data) {
      const find = _item => _item.id === data.id;
      remove(this.materialCatalogs, find);
    },
    async refresh() {
      this.timestamp = Date.now();
    },
    toggleSelected(material, selected) {
      toggleFrom(this.selected, material, selected);
    },
    clearAndSelect(catalog) {
      this.clearSelected();
      this.toggleSelected(catalog, true);
    },
    handleContextmenu(e, catalog, background) {
      e.preventDefault();
      if (!catalog || (catalog && !this.selected.includes(catalog))) {
        this.clearSelected();
      }
      if (catalog) {
        this.toggleSelected(catalog, true);
      }
      this.$emit('contextmenu', e, this.selected.length ? this.selected : undefined, background);
    },
    clearSelected() {
      this.selected.splice(0, this.selected.length);
    },
    handleListDragStart(e) {
      this.$emit('drag', e, this.selected);
    },
    // 显示面料信息弹框，将当前面料对象存储
    async handleShoWDialogInfo(material) {
      try {
        if (material.goodId) {
          openSingleton({
            path: '/material-mall/goods-detail',
            query: {
              id: material.goodId,
              materialId: material.materialId,
              fromLibrary: true,
            },
          }, true, 'GoodsDetail');
          return;
        }
        const businessType = material.businessType ?? 0;

        await this.$Dialog.dialogify({
          is: businessType === 1 ? AntaMaterialInfo : MaterialInfo,
          props: {
            id: material.id,
            groupAppId: this.rootCatalog.workspaceId,
            // 渲染页，不允许编辑面料详情
            editable: false,
          },
        }, {
          props: {
            top: 'auto',
            bottom: 'auto',
          },
        });
      } catch (err) {
        this.$errHandler(err);
      }
    },

    // 显示放大镜效果，通过点击小按钮触发
    showMagnifyMode(index) {
      const target = this.displayMode === 'drape' ? 'contentThumbnail' : 'flat';
      const srcList = this.materials.map(e => {
        return {
          src: e[target] || e.thumbnail,
        };
      });

      this.$ImageViewer.show(srcList, {
        index,
        targetElems: '[data-img-type=\'material-list\']',
      });
    },
  },
};
</script>

