<template>
  <ul class="upload-images-wrapper" ref="pictures">
    <li v-for="(img, index) of pImgs"
      :key="index" class="images-item">
      <!-- 上传时的遮罩层 -->
      <div class="modal" v-if="img.percentage !== 100">
        <!-- 进度条 -->
        <el-progress
          :width="70"
          type="circle" :percentage="img.percentage"></el-progress>
      </div>

      <!-- 图片 -->
      <div class="image">
        <BaseImage :src="img.thumb" :data-src="img.thumb"
          fit="cover" :thumb="{ width: 80, height: 80 }" />
        <!-- 悬浮在图片时的遮罩层 -->
        <div class="modal2" @click.stop="handleViewImage(index)"
          :data-img-type='`image-uploader-${_uid}`'>
          <!-- 删除按钮 -->
          <span class="iconfont icon-delete" @click.stop="handleDeleteImage(index)"></span>
        </div>
      </div>
    </li>
    <li @click="$refs.inputFile.click()" class="btn-add" v-show="pImgs.length < 9">
      <input
        ref="inputFile"
        type="file"
        @change="uploadImg"
        accept='image/*'
        style="display: none;"
        multiple />
    </li>
  </ul>
</template>

<script>
import i18n from 'i18next';
import {
  getItem,
} from '@/utils/oss/utils';

import {
  OSSUploader,
} from '@/utils/oss/uploader';
import BaseImage from '@/components/base/Image';

function asyncForEach(arr, iterator, ...args) {
  return Promise.all(Array.prototype.map.call(arr, (...args2) => {
    return iterator(...args2);
  }), ...args);
}

export default {
  components: {
    BaseImage,
  },
  props: {
    pImgs: Array,
  },
  data() {
    return {
      imgs: [],
      initialViewIndex: 0,
    };
  },
  model: {
    prop: 'pImgs',
    event: 'updateImgs',
  },
  methods: {
    // 显示并上传图片
    async uploadImg() {
      if (!this.pImgs) this.pImgs = [];
      // 检测文件数量
      const length = this.$refs.inputFile.files.length + this.pImgs.length;
      if (length > 9) {
        this.$message.error(i18n.t('model.comment.images_maximum'));
        return;
      }

      const uploader = new OSSUploader({ sourceType: 'COMMENT', sendAs: 'arrayBuffer' });

      try {
        const tmpImgs = this.pImgs.slice();
        await asyncForEach(this.$refs.inputFile.files, async (uploadedFile) => {
          // 检测大小，过大会出错
          if (uploadedFile.size > 5 * 1024 * 1024) {
            throw new Error(i18n.t('model.comment.image_less_than'));
          }
          // 获得key,防重复
          const item = await getItem(uploadedFile,
            { sourceType: 'COMMENT' });
          const { path, key } = item;

          if (tmpImgs.length && tmpImgs.filter(each => each.path === path).length > 0) {
            throw new Error(i18n.t('model.comment.duplicated_image'));
          }

          // 获得用于显示的url
          const thumb = await URL.createObjectURL(uploadedFile);

          const file = {
            ...item,
            thumb,
            percentage: 0,
          };

          tmpImgs.push(file);

          // 上传到oss
          await uploader.send({
            params: { key },
            body: uploadedFile,
            onUpload(...args) {
              file.percentage = parseInt(args[3] * 100, 10);
            },
          });
        });

        this.$emit('upload-img', tmpImgs);
      } catch (e) {
        this.$message.error(e.message);
      }
    },

    // 删除某张图片
    handleDeleteImage(index) {
      const tmpImgs = this.pImgs.slice();
      tmpImgs.splice(index, 1);
      // 防重复
      this.$refs.inputFile.value = null;
      this.$emit('upload-img', tmpImgs);
      // this.imgs.splice(index, 1);
    },

    // 看大图
    handleViewImage(index) {
      const srcList = this.pImgs.map(e => {
        return {
          src: e.thumb,
        };
      });
      this.$ImageViewer.show(srcList, {
        index,
        targetElems: `[data-img-type=image-uploader-${this._uid}]`,
      });
    },
  },
  watch: {
    // imgs(newVal) {
    //   this.$emit('on-upload-img', newVal);
    // },
    // pImgs(newVal) {
    //   this.imgs = newVal;
    // },
  },
};
</script>

