<template>
  <div class="create-board container" ref="createBoard">
    <div class="search-bar">
      <SearchAndSelect ref="searchbar" :value="materialSearchOptions" @change="handleSearch" />
      <el-button size="small" @click="createFolder" v-if="isCatalogCanCreateFolder(catalog)" class="flex-center">
        <i class="iconfont icon-folder-add"></i>
      </el-button>
      <!-- <el-button
        @click="$act(showDialogView)"
        size="small"
        v-if="isCatalogCanToggleHide(catalog)">
        <i class="iconfont icon-eye"></i>
      </el-button> -->
      <el-button size="small" @click="refresh" class="flex-center">
        <i class="iconfont icon-refresh-2"></i>
      </el-button>
    </div>
    <div style="flex: 1; position: relative">
      <div style="top: 0; bottom: 0; right: 0; left: 0; position: absolute; display: flex; flex-direction: column">
        <Scrollable
          y
          class="tree-container"
          @click.native.self="currentCatalog = null"
          @contextmenu.native="(e) => $act(setContextAndShowMenuBackground, [e, { data: catalog }])"
        >
          <div class="tree-wrapper" v-loading="catalog.children === undefined">
            <tree
              :dragging-data="draggingData"
              @update:draggingData="updateDraggingData"
              :expandable="isCatalogExpandable"
              :draggable="isCatalogCuttable"
              :droppable="isCatalogDroppable"
              :hidden="isCatalogHidden"
              :show-other="isCatalogCanShowOther"
              :data="catalog"
              :key="catalog.time"
              :load="getChildren"
              @drop="(...args) => $act(paste, args)"
              @contextmenu="(...args) => $act(setContextAndShowMenu, args)"
              @othercontextmenu="() => {}"
              :opened.sync="openedCatalogs"
            >
              <div class="tree-empty" slot="empty">
                {{ $t("model.detail.board.no_folder") }}
              </div>
              <template v-slot:other="{ data }">
                <div
                  class="tree-other"
                  :class="{
                    selected: currentCatalog === data.other,
                  }"
                  @click="selectCatalog(data.other)"
                >
                  <i class="iconfont icon-unclassified" />
                  <span class="tree-title">
                    {{ $t("model.detail.board.unclassified") }}
                  </span>
                  <!-- <span class="tree-count"
                    v-if="data.fileCount" style="margin-left: .5em">{{
                      data.fileCount > 100 ? 99 : data.fileCount
                    }}</span> -->
                </div>
              </template>
              <template v-slot="{ data, isOpened }">
                <div
                  class="tree-item"
                  @click="handleClick(data)"
                  :class="{
                    hidden: isCatalogHidden(data),
                    selected: currentCatalog === data,
                    moving: isCatalogMoving(data),
                    copying: isCatalogCopying(data),
                  }"
                  tabindex="0"
                >
                  <i class="iconfont" :class="isOpened ? 'icon-folder-open' : 'icon-folder-close'" />
                  <input
                    v-if="renamingCatalog === data"
                    class="tree-rename-input"
                    :value="data.name"
                    :ref="`rename-input-${data.id}`"
                    @blur="renameCatalog(data, $event.target.value, $event.target)"
                  />
                  <span class="tree-title" v-else>
                    {{ data.name }}
                  </span>
                  <span class="tree-count" v-if="data.allFileCount">{{
                    data.allFileCount > 100 ? 99 : data.allFileCount
                  }}</span>
                  <div class="tree-icons">
                    <!-- <i class="iconfont"
                      v-if="isCatalogCanToggleHide(data)"
                      :class="isCatalogHidden(data) ? 'icon-eye-slash': 'icon-eye'"
                      @click.stop="toggleHideCatalog(data, parent)" /> -->
                  </div>
                </div>
              </template>
            </tree>
          </div>
        </Scrollable>

        <div class="middle-part" @mousedown.stop.prevent="handleDragstart"></div>

        <!-- 搜索结果展示区 -->
        <Scrollable class="materialList-wrapper" y style="height: 50%" ref="materialList">
          <material-list
            :rootCatalog="catalog"
            :catalog="currentCatalog"
            :searchParams="materialSearchParams"
            @drag="(e, data) => handleMaterialDrag(e, data)"
            @contextmenu="(e, data, background) => $act(setMaterialContextmenu, [e, { data: data }, background])"
            @pick="updateMaterial"
            @update:displayMode="updateDisplayMode"
            :display-mode="displayMode"
          >
            <div slot="brief">{{ searchBrief }}</div>
            <template v-slot:empty>
              <template v-if="!materials">
                <h4 style="font-weight: normal">
                  <template v-if="catalog.entryAppType === 12"> {{ $t("model.detail.board.entryType_12") }}, </template>
                  <template v-if="catalog.entryAppType === 13"> {{ $t("model.detail.board.entryType_13") }}, </template>
                  <template v-if="catalog.entryAppType === 16"> {{ $t("model.detail.board.entryType_16") }}, </template>
                </h4>

                {{ $t("model.detail.board.select_folder") }}
                <div class="to-help-center-wrapper">
                  {{ $t("model.detail.need_more_help") }}&nbsp;
                  <router-link
                    tag="a"
                    :to="{
                      name: 'HelpCenterArticle',
                      params: {
                        pathMatch: '材质工作面板能为你做什么？',
                      },
                    }"
                    class="a-link"
                  >
                    {{ $t("model.detail.go_to_help_center") }}</router-link
                  >
                </div>
              </template>
              <template v-else-if="materialSearchOptions">
                {{ $t("model.detail.board.no_search_result_tip") }}
              </template>
              <template v-else>
                {{ $t("model.detail.board.empty_folder_tip") }}
              </template>
            </template>
          </material-list>
        </Scrollable>
      </div>
    </div>
  </div>
</template>

<script>

import i18n from 'i18next';

import Tree from '@/components/Tree';
import Scrollable from '@/components/Scrollable';
import draggingData from '@/utils/dragging-data';
import {
  getHiddenCatalog,
  updateResourceItemStatus,
} from '@/api/catalogue';
import SearchAndSelect from '@/views/file-list/components/catalog/Search&Select';
import { renameCatalog } from '@/loaders/catalog';
import remove from '_/remove';
import {
  commands,
} from '@/utils/catalog';
import clipboard from '@/utils/clipboard';
import axios from '@/utils/axios';
import useMaterialTypes from '@/utils/composables/use-business-types';
import {
  materialColorPicker,
} from '@/views/material/form-related';
import Vue from 'vue';
import keys from '_/keys';
import { getSetting, setSetting } from '../../../../loaders/setting';
import FolderSelect from './FolderSelect';
import MaterialList from './MaterialList';

const HIDDEN_STATUS = 13;
const NORMAL_STATUS = 11;

export function isCatalogExpandable(item) {
  return item.folderCount;
}
export async function updateCatalogStatus(item, status) {
  await updateResourceItemStatus([item.id], status);
  item.status = status;
}
export function isCatalogHidden(item) {
  return item.status === HIDDEN_STATUS;
}
export function toggleHideCatalog(item) {
  if (!isCatalogHidden(item)) {
    return updateCatalogStatus(item, HIDDEN_STATUS);
  }
  return updateCatalogStatus(item, NORMAL_STATUS);
}

function ensureAdd(from, item, fn) {
  const index = typeof item === 'function' ? from.findIndex(fn) : from.indexOf(item);
  if (index === -1) {
    from.push(item);
  }
}
function addOther(_item) {
  const toJSON = () => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { other, item: _i, ...json } = _item;
    return json;
  };
  Vue.set(_item, 'other', { ..._item, item: _item, toJSON });
  _item.toJSON = toJSON;
}
export default {
  setup() {
    return {
      xMaterialTypes: useMaterialTypes(),
    };
  },
  props: {
    catalog: {},
    role: String,
  },
  components: {
    Tree,
    MaterialList,
    Scrollable,
    SearchAndSelect,
  },
  data() {
    this._cacheCatalogs = {};
    this._materialCache = {};
    this.catalog.time = Date.now();
    if (!this.catalog.other) {
      addOther(this.catalog);
    }
    return {
      hiddenCatalogs: [],
      openedCatalogs: [this.catalog],
      renamingCatalog: undefined,
      currentCatalog: undefined,
      materialSearchOptions: undefined,
      searchBrief: '',
      materialOptions: [],
      searchOptions: [],
    };
  },
  watch: {
    async catalog(categories) {
      this.openedCatalogs = [categories];
    },
    renamingCatalog(v) {
      if (v) {
        this.focusRenameInput(v);
      }
    },
  },
  async mounted() {
    // const res = await getCatalogIds();
    // this.rootCatalogId = res['12'];
    // console.log(this.rootCatalogId);
  },
  computed: {
    materialSearchParams() {
      if (!this.materialSearchOptions) return undefined;
      const v = this.materialSearchOptions;
      const { filters } = v;
      return {
        keyword: v.keyword,
        relateType: [1],
        ...keys(filters).reduce((memo, key) => {
          const value = filters[key];
          if (value && value.length) {
            if (key === 'materialClassifications') {
              memo[key] = value.slice(1).join('_');
            } else {
              memo[key] = value[value.length - 1];
            }
          }
          return memo;
        }, {}),
      };
    },
    /** @return {object} */
    materialTypes() {
      const list = this.xMaterialTypes;
      if (!list) {
        return {
          value: 'MaterialTypes',
          label: i18n.t('file-list.material_type'),
          children: [],
        };
      }
      return {
        value: 'MaterialTypes',
        label: i18n.t('file-list.material_type'),
        children: list.map(item => {
          return {
            label: item.label,
            value: item.itemValue,
          };
        }),
      };
    },
    /** @return { any } */
    colorFamily() {
      const lang = i18n.language;
      const child = materialColorPicker.map(item => {
        return {
          label: lang === 'cn' ? item.cnName : item.name,
          value: item.name,
        };
      });
      return {
        value: 'ColorFamilys',
        label: i18n.t('file-list.color_family'),
        children: child,
      };
    },
    /** @return { any } */
    draggingData() {
      return draggingData.get();
    },
    /** @return { string } */
    displayMode() {
      return getSetting('modelDetailViewType', 'flat');
    },
    /** @return { any } */
    materialsParent() {
      return this.currentCatalog?.item ?? this.currentCatalog;
    },
  },
  methods: {
    updateMaterial(catalog) {
      const model = this.$closest('model');
      model.updateMaterial(catalog.fileInfo);
    },
    // 显示显示其他隐藏目录弹框
    async showDialogView() {
      const item = this.catalog;
      const ids = await this.$Dialog.dialogify({
        is: FolderSelect,
        props: {
          list: (await getHiddenCatalog(item.id, item.workspaceId)).data,
        },
      }, {
        props: {
          width: '370px',
          title: i18n.t('model.detail.board.show_hidden_folder'),
        },
      });
      await updateResourceItemStatus(ids, 11, this.catalog.workspaceId);
      return this.refresh();
    },
    refresh() {
      this.catalog.time = Date.now();
      this.catalog.children = undefined;
      this.openedCatalogs = [this.catalog];
      return this.fetchChildCatalog(this.catalog);
    },
    handleMaterialDrag(e, data) {
      if (data.length) {
        draggingData.set({
          type: 'catalog',
          command: 'cut',
          parent: this.currentCatalog,
          data,
        });
      }
    },
    handleDragstart(event) {
      const startY = event.clientY;
      const materialListHeight = parseInt(getComputedStyle(this.$refs.materialList.$el).height, 10);
      this.$el.classList.add('is-dragging');
      const handleDrag = (e) => {
        const deltaY = startY - e.clientY;
        this.$refs.materialList.$el.style.height = `${materialListHeight + deltaY}px`;
      };
      document.addEventListener('mousemove', handleDrag, false);
      const handleDragend = () => {
        this.$el.classList.remove('is-dragging');
        document.removeEventListener('mousemove', handleDrag, false);
        document.removeEventListener('mouseup', handleDragend, false);
      };
      document.addEventListener('mouseup', handleDragend, false);
    },
    isInClipBoard(item) {
      return (clipboard.datum === item
        || (Array.isArray(clipboard.data)
          && clipboard.data.indexOf(item) !== -1));
    },
    isCatalogMoving(item) {
      return this.isInClipBoard(item) && clipboard.command === 'cut';
    },
    isCatalogCopying(item) {
      return this.isInClipBoard(item) && clipboard.command === 'copy';
    },
    handleClick(item) {
      if (this.currentCatalog === item) {
        this.switchRename(item);
      } else {
        this.selectCatalog(item);
      }
    },
    async switchRename(item) {
      if (this.currentCatalog !== item) {
        await this.selectCatalog(item);
      }
      if (commands.rename.check({ data: item })) {
        this.renamingCatalog = item;
      }
    },
    async selectCatalog(item) {
      this.currentCatalog = item;
      this.materialSearchOptions = undefined;
    },
    async setMaterialContextmenu(e, scope, background) {
      if (!scope.data || !scope.data.length) {
        if (this.materialSearchOptions) {
          scope = {
            data: {
              entryAppType: this.catalog.entryAppType,
              searchParams: this.materialSearchOptions,
              relateName: this.relateName,
              workspaceId: this.catalog.workspaceId,
            },
          };
        } else {
          scope = {
            data: this.materialsParent,
          };
        }
      }
      if (background) {
        if (scope.data) {
          return this.setContextAndShowMenu(e, scope, {
            copy: false,
            cut: false,
            rename: false,
            remove: false,
            createFolder: false,
            uploadPDF: false,
            uploadFile: false,
            uploadImage: false,
          });
        }
        return undefined;
      }
      return this.setContextAndShowMenu(e, scope);
    },
    async setContextAndShowMenu(e, scope, mixCommands) {
      const that = this;
      await this.$openContextMenu(e, {
        ...scope,
        type: 'catalog',
      }, {
        addShortcut: false,
        uploadModel: false,
        uploadMaterial: true,
        toggleHide: false,
        shareToIm: false,
        uploadPDF: false,
        upload: false,
        uploadImage: false,
        createFolder({ parent, data }) {
          that.afterCreateFolder(parent, data);
        },
        paste({ from, to }) {
          that.afterPaste({ from, to });
        },
        rename({ datum, name }) {
          datum.name = name;
        },
        remove(s) {
          that.remove(s);
        },
        ...mixCommands,
      });
    },
    async setContextAndShowMenuBackground(e, scope) {
      return this.setContextAndShowMenu(e, scope, {
        copy: false,
        cut: false,
        rename: false,
        remove: false,
        uploadPDF: false,
        upload: false,
        uploadImage: false,
      });
    },
    async createFolder() {
      let item = this.catalog;
      if (this.currentCatalog) {
        if (this.currentCatalog.item) {
          // eslint-disable-next-line prefer-destructuring
          item = this.currentCatalog.item;
        } else {
          item = this.currentCatalog;
        }
      }
      const newFolder = (await commands.createFolder.fn({ data: item })).data;
      this.afterCreateFolder(item, newFolder);
    },
    updateDraggingData(v) {
      if (v) {
        const { data: item, ...other } = v;
        const { other: _, ...data } = item;
        draggingData.set({
          data, command: 'cut', type: 'catalog', ...other,
        });
      } else {
        draggingData.clear();
      }
    },
    updateDisplayMode(v) {
      setSetting('modelDetailViewType', v);
    },
    isCatalogHidden(item) {
      return !this.showHidden && isCatalogHidden(item);
    },
    isCatalogExpandable,
    isCatalogDroppable(s) {
      return commands.paste.check(s.from, { ...s.to, type: 'catalog' });
    },
    isCatalogCanCreateFolder(data) {
      return commands.createFolder.check({ data });
    },
    isCatalogCanToggleHide(data) {
      return commands.toggleHide.check({ data });
    },
    isCatalogCuttable(data) {
      return commands.cut.check({ data });
    },
    isCatalogCanShowOther() {
      return true;
    },

    async focusRenameInput(item) {
      await this.$nextTick();
      this.$refs[`rename-input-${item.id}`].focus();
    },
    cache(map, _item) {
      let cache = map[_item.id];
      if (cache) {
        Object.assign(cache, _item);
      } else {
        map[_item.id] = _item;
        cache = _item;
      }
      return cache;
    },
    async handleSearch(v) {
      this.currentCatalog = null;
      this.materialSearchOptions = v;
    },
    async renameCatalog(item, name, input) {
      try {
        name = name.toString().trim();
        if (!name) {
          throw new this.$Error(i18n.t('model.detail.board.input_valid_name'));
        }
        await renameCatalog({ id: item.id, name }, item.workspaceId);
        item.name = name;
        this.renamingCatalog = null;
      } catch (e) {
        if (input) {
          input.focus();
        }
        this.$errHandler(e);
      }
    },
    openCatalog(item) {
      ensureAdd(this.openedCatalogs, item);
    },
    closeCatalog(item) {
      remove(this.openedCatalogs, item);
    },
    toggleHideCatalog(item, parent) {
      toggleHideCatalog(item);
      if (!this.isCatalogHidden(item)) {
        parent.folderCount -= 1;
      }
    },
    async paste({ from, to }) {
      await commands.paste.fn(to, from);
      await this.afterPaste({ from, to });
    },
    async fetchChildCatalog(item) {
      let children = (await axios.get('bff-center/cloud-disk/list/getRenderMaterialChildCatalogBy', {
        params: {
          catalogId: item.id,
        },
        headers: {
          groupAppId: item.workspaceId ?? '',
        },
      })).data;
      if (children) {
        children = children.map((_item) => {
          _item.entryAppType = item.entryAppType;
          _item.parentFolderId = _item.parentFolderId || item.id;
          _item.workspaceId = item.workspaceId;
          addOther(item);
          return this.cache(this._cacheCatalogs, _item);
        });

        if (item.children) {
          item.children.splice(0, item.children.length, ...children);
          // eslint-disable-next-line prefer-destructuring
          children = item.children;
        }
        item.folderCount = children.length;
      }
      this.$set(item, 'children', children);
      return item.children;
    },
    async getChildren(item) {
      let { children } = item;
      if (!children) {
        if (isCatalogExpandable(item)) {
          children = await this.fetchChildCatalog(item);
        } else {
          children = [];
          this.$set(item, 'children', children);
        }
      }
      return children;
    },
    async afterPaste({
      from: {
        datum, parent, index, fileType, relateType, command,
      },
      to: {
        data: targetParent, index: targetIndex = 0, moveIndex: targetMoveIndex = 0,
      },
    }) {
      // 如果是操作目录
      if (!fileType) {
        if (command === 'cut') {
          datum.entryAppType = targetParent.entryAppType;
          datum.parentFolderId = targetParent.id;
          datum.workspaceId = targetParent.workspaceId;
          parent.children.splice(index, 1);
          if (parent) {
            parent.folderCount = parent.children.length;
            if (!parent.folderCount) {
              this.closeCatalog(parent);
            }
          }
          await this.openCatalog(targetParent);
          targetParent.children.splice(command === 'cut' ? targetMoveIndex : targetIndex, 0, datum);
          targetParent.folderCount = targetParent.children.length;
        } else {
          await this.fetchChildCatalog(targetParent);
          this.openCatalog(targetParent);
        }
      } else if (relateType) {
        if (command === 'cut') {
          parent.fileCount -= 1;
        }
        targetParent.fileCount += 1;
        // 如果是操作面料
        if (targetParent === this.currentCatalog || targetParent.other === this.currentCatalog) {
          this.$refs.materialList.refresh();
        } else {
          await this.selectCatalog(targetParent);
        }
      }
    },
    async afterCreateFolder(item, data) {
      await this.getChildren(item);
      await this.$nextTick();
      this.openCatalog(item);
      item.folderCount += 1;
      this.cache(this._cacheCatalogs, data);
      item.children.unshift(data);

      // this.switchRename(data);
    },

    async remove({
      data, parent, fileType, relateType,
    }) {
      const find = _item => _item.id === data.id;
      if (!fileType) {
        remove(parent.children, find);
        parent.folderCount -= 1;
        if (!parent.folderCount) {
          this.closeCatalog(parent);
        }
      } else if (relateType) {
        if (this.currentCatalog.id === data.parentFolderId) {
          const item = this.materialsParent;
          item.fileCount -= 1;
          this.$refs.materialList.remove(data);
        }
      }
    },

  },

};
</script>

<style lang="scss" scoped>
$hoverColor: #d7e4fb;
$borderColor: #eee;

.create-board {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.search-bar {
  margin-left: 20px;
  margin-right: 20px;
}
.tree-container {
  flex: 1;
}
.tree-wrapper {
  padding-left: 20px;
  padding-right: 7px;
  margin-right: 13px;
  padding-top: 10px;
}
.material-list {
  margin-left: 20px;
  margin-right: 20px;
  height: 100%;
}

.middle-part {
  padding: 8px 0;
  &:before {
    content: "";
    display: block;
    height: 2px;
    background-color: #f4f7fc;
  }
  cursor: row-resize;
}

@keyframes blink {
  50% {
    border-color: $color-primary;
  }
}

.loading {
  min-height: 81px;
}

.materialList-wrapper {
  // border-top: $borderColor 2px solid;
  min-height: 192px;
  max-height: calc(100% - 192px);
}

.is-dragging {
  .tree-wrapper,
  .material-list {
    cursor: row-resize;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0);
      z-index: 1010;
    }
  }
}

.tree {
  &-wrapper {
    height: 150px;
    min-height: 81px;
  }
  .count {
    float: right;
  }
  &-empty {
    padding-bottom: 6px;
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px;
    color: #999;
    text-align: center;
  }
  &-item,
  &-other {
    .iconfont {
      margin-right: 5px;
      font-size: 130%;
    }
    color: #777;
    align-content: center;
    align-items: center;
    display: flex;
    line-height: 24px;
    cursor: pointer;
    padding-bottom: 5px;
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px;
    overflow: hidden;
    position: relative;
    border-radius: 4px;
    border-bottom: 1px solid;
    border: 1px solid transparent;
    border-bottom-color: $borderColor;

    &:hover,
    &:focus {
      outline: none;
      background-color: $hoverColor;
      color: $color-primary;
      border-color: darken($hoverColor, 10%);
    }
    &.selected {
      color: white;
      background-color: $color-primary;
      border-color: $color-primary;
      .dragover > .content > .item-wrapper > & {
        color: white;
      }
    }
  }
  &-item {
    &:hover {
      .tree-count {
        display: none;
      }
      .tree-icons {
        display: block;
      }
    }
    &.hidden {
      color: #999;
