          <span class="user-name text-one-line">{{ $t('model.detail.unknown_user') }}</span>
        </template>
      </p>

      <time class="version-time">{{ item.gmtCreateTime | date('MM/DD/YYYY HH:mm') }}</time>
    </li>
  </ul>
