<template>
  <div class="model-content" :class="modeClass" ref="modelContent">
    <!-- 鞋模型渲染 -->
    <section class="left-part" ref="leftPart" @select.stop.prevent="()=>false">
      <div class="before"></div>
      <renderer-component ref="renderer" :model="model" :key="modelId" @add-annotation="refreshAnnotation" />
    </section>

    <div class="middle-part" @mousedown="handleDragBoard"></div>

    <!-- 工作面板 -->
    <section
      class="right-part"
      ref="rightPart"
      id="right">
      <div class="before"></div>
      <AnnotationComponent v-if="annotationStatus && model" style="flex: 1"
        @annotation-display="annotationDisplay"
        @model-preview="modelPreview"
        @annotation-remove="removeAnnotation"
        @close="annotationStatus = false; (renderer && renderer.recoverCameraType())"
        :workspaceId="workspaceId"
        :modelId="modelId"
        :modelName="model.modelNumber"
        :activeId="signCurrentId"
        :catalogId="catalogId" ref="annotation" />
      <RenderPhoto v-else-if="photoStatus" :from="workspaceId" />
      <board-component
        v-else
        :data-no-permission="!$can('workspace', 'OP0014')"
        class="board-component"
        @on-change-mode="handleVisionModeChange" />
    </section>
  </div>
</template>

<script>
import axios from '@/api/axios';
import { i18n } from '@/i18n';
import { getModelInfo } from '@/api/shoe';
import { openSingleton } from '@/utils/open-window';
import RenderPhoto from '../offline/RenderPhoto';
import RendererComponent from './renderer';
import BoardComponent from './board';
import AnnotationComponent from './annotation';

const getHandbagInfo = (handbagId) => {
  return axios.get('/bff-center/showCommon/handbagInfoSearch/getHandbagInfoAndTags', {
    params: {
      handbagId,
    },
    headers: {
      language: i18n.language,
    },
  });
};

export default {
  name: 'model',
  components: {
    RendererComponent,
    BoardComponent,
    AnnotationComponent,
    RenderPhoto,
  },
  props: ['modelId', 'workspaceId', 'catalogId'],
  data() {
    return {
      mode: 'normal',
      startX: 0,
      minWidth: 0,
      model: null,
      annotationStatus: this.$route.query.showAnnotation === 'true',
      photoStatus: false,
    };
  },
  computed: {
    /** @return {string} */
    signCurrentId() {
      return this.$refs.renderer?.signCurrent?.id;
    },
    /** @return {string} */
    modeClass() {
      // eslint-disable-next-line
      return this.startX ? 'mode-dragging' : this.mode === 'normal' ? 'mode-nomal' : 'mode-edit';
    },
    /** @return {Vue} */
    rightPart() {
      return this.$refs.rightPart;
    },
    /** @return {Vue} */
    leftPart() {
      return this.$refs.leftPart;
    },
    /** @return {Vue} */
    renderer() {
      return this.$refs.renderer;
    },
    /** @return {Vue} */
    annotation() {
      return this.$refs.annotation;
    },
  },
  watch: {
    annotationStatus(val, oldVal) {
      if (val) {
        this.renderer.stashCameraType();
      }
    },
  },
  created() {
    this.$watch('modelId', () => {
      this.init();
    }, {
      immediate: true,
    });
  },
  methods: {
    removeAnnotation(ann) {
      const { renderer } = this;
      if (renderer) {
        renderer.d4.deleteSign(ann.id);
        if (renderer.signCurrent && renderer.signCurrent.id === ann.id) {
          renderer.signCurrent = null;
        }
      }
    },
    refreshAnnotation() {
      this.annotation.fetchAnnotationList();
    },
    annotationDisplay({ annotation, readonly }) {
      this.renderer.showAnnotation(annotation, readonly);
    },
    modelPreview({ modelId, catalogId, workspaceId }) {
      openSingleton({
        name: 'ModelPreview',
        params: {
          modelId,
        },
        query: {
          workspaceId,
          catalogId,
          showAnnotation: true,
        },
      }, true, 'ModelPreview');
    },
    async init() {
      if (this.$route.query.relateType === '3') {
        this.model = (await getHandbagInfo(this.modelId)).data;
      } else {
        this.model = (await getModelInfo(this.modelId)).data;
      }
    },
    save(...args) {
      return this.renderer.save(...args);
    },
    updateMaterial(...args) {
      return this.renderer.updateMaterial(...args);
    },
    // 切换浏览模式（board 与 renderer 框宽度比例切换）
    handleVisionModeChange() {
      this.mode = this.mode === 'normal' ? 'edit' : 'normal';
    },
    handleDragstart(event) {
      this.startX = event.clientX;
      this.startWidth = parseInt(getComputedStyle(this.rightPart).width, 10);
      // const document = this.rightPart.ownerDocument;
      // const handler = (e) => {
      //   this.handleDragThrottler(e);
      // };
      // document.addEventListener('mousemove', handler, false);
      // console.log('dragstart');
    },
    // eslint-disable-next-line
    handleDragThrottler(event) {
      const distance = event.clientX - this.startX;
      if (distance === 0) return;

      if (event.clientX) {
        this.rightPart.style.width = `${this.startWidth - distance}px`;
      }
    },
    handleDragBoard(e) {
      this.startX = e.clientX;
      this.startWidth = parseInt(getComputedStyle(this.rightPart).width, 10);
      const handler = (e2) => {
        const distance = e2.clientX - this.startX;
        if (distance === 0) return;

        if (e2.clientX) {
          // 若小于默认宽度则使用默认宽度
          this.rightPart.style.width = this.startWidth - distance <= parseInt(this.minWidth, 10) ? this.minWidth : `${this.startWidth - distance}px`;
        }
      };
      const handler2 = () => {
        this.startX = null;
        this.renderer.resize();
        document.removeEventListener('mousemove', handler, false);
        document.removeEventListener('mouseup', handler2, false);
      };
      document.addEventListener('mousemove', handler, false);
      document.addEventListener('mouseup', handler2, false);
    },
  },
};
</script>

<style lang="scss" scoped>
.board-component {
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.model-content {
  :fullscreen & {
    .middle-part,
    .right-part {
      display: none;
    }
  }

  min-height: 700px;
  width: 100%;
  margin-top: 10px;
  flex: 1;
  display: flex;
  &.mode-dragging {
    cursor: col-resize;
    .left-part,
    .right-part {
      transition: none;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0);
        z-index: 1010;
      }
    }
  }
  .left-part {
    position: relative;
    flex: 1;
    background-color: #fff;
    overflow: hidden;
    min-width: 770px;
    transition: all ease-in-out 400ms;
  }

  .middle-part {
    width: 10px;
    background-color: transparent;

    cursor: col-resize;
  }

  .right-part {
    flex-direction: column;
    display: flex;
    position: relative;
    width: 462px;
    min-width: 356px;
    max-width: 674px;
    background-color: #fff;
    transition: all ease-in-out 400ms;
  }

  .no-permission {
    position: absolute;
    z-index: 1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 0.7);
  }
}
</style>
