<template>
  <!-- 容器 -->
  <div class="comment-list__item">

    <!-- 根据设计稿，左边为头像，右边为用户和评论信息 -->
    <!-- 用户头像 -->
    <div class="icon-container">
      <BaseImage class="avatar" :defaultImage="defaultAvatar" :src="user.avatar" />
    </div>

    <!-- 包括用户信息和评论信息的容器 -->
    <section class="comment-wrapper">

      <!-- 上方为用户信息 -->
      <div class="user-info">
        <!-- 第一行包括用户名、版本号和创建时间 -->
        <div style="display:flex;align-items:center;">
          <span class="username">
            {{ user.name || user.account }}
          </span>
          <span class="version-card" v-if="showVersion">
            {{ info.versionName }}
          </span>
          <span style="flex: 1"></span>
          <span class="created-time">
            {{ info.time | date('MM/DD/YYYY HH:mm') }}
          </span>

          <!-- 允许用户编辑和删除自己的评论 -->
          <el-dropdown trigger="click"
            v-if="currentUser.id === user.id && !readonly"
            class="self-dropdown-list"
            placement="bottom">
            <div class="ellipsis-container">
              <span class="iconfont icon-ellipsis-vertical"></span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="handleClickEdit">
                <i class="iconfont icon-pencil-o"></i>
                <span>
                  {{ $t('model.comment.edit') }}
                </span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="handleClickDelete">
                <i class="iconfont icon-file-delete"></i>
                <span>
                  {{ $t('model.comment.delete') }}
                </span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>

        </div>

        <!-- 第二行包括部门，是否编辑过 -->
        <div style="display:flex;margin-top:.8rem;">
          <span class="department">
            {{ user.department }}
          </span>
          <span style="flex: 1"></span>
          <span v-if="info.status === 2" class="flag-modified">
            {{ $t('model.comment.modified') }}
          </span>
        </div>
      </div>

      <!-- 下方为评论信息 -->
      <div class="comment">
        <div v-if="mode === 'view'" class="comment-content-wrapper" v-html="content">
          <!-- {{ content }} -->
        </div>
      </div>
      <ul class="comment-pictures" v-if="mode === 'view'"
        ref="comment_pictures">
        <li class="comment-pictures__item" v-for="(pic, index) of info.commentImgs || info.imgs"
          :key="index">
          <BaseImage :data-src="pic.url" :src="pic.url" :data-img-type='`comment-item-${_uid}`'
            class="comment-picture" @click.native="handleViewPicture(index)" fit="cover" />
        </li>
      </ul>

      <!-- 最下方为编辑模式的按钮 -->
      <!-- <div v-if="mode === 'edit'" class="comment-footer">
        <el-input type="textarea" v-model="comment"></el-input>
        <CommentPictureEditor mode="edit" />

        <div class="button-group">
          <el-button @click="mode='view'">Cancel</el-button>
          <el-button type="primary">OK</el-button>
        </div>
      </div> -->

      <div v-if="mode === 'edit'">
        <comment-add mode="edit" :id="info.id"
          :pComment="info.content || info.comment"
          :pCommentImgs="info.imgs || info.commentImgs"
          @on-edit-comment="mode='view';$emit('on-edit-comment')"
          :members="atableMembers" />
      </div>
    </section>

  </div>
</template>

<script>
import i18n from 'i18next';
// import CommentPictureEditor from './CommentPictureEditor';
import BaseImage from '@/components/base/Image';
// import 'viewerjs/dist/viewer.css';
import defaultAvatar from '@/assets/img/header/default_avatar.png';

import {
  DeleteComment,
} from '@/api/comment';
import { getContextUser } from '../../../../loaders/context';
import CommentAdd from './CommentAdd';

export default {
  components: {
    // CommentPictureEditor,
    CommentAdd,
    BaseImage,
  },
  props: {
    info: {
      type: Object,
      required: true,
    },
    atableMembers: Array,
    showVersion: {
      type: Boolean,
      default: true,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    user() {
      return this.info.user;
    },
    currentUser() {
      return getContextUser();
    },
    content() {
      return this.account2Username(this.info.content,
        this.atableMembers,
        this.user);
    },
  },
  data() {
    return {
      mode: 'view', // view or edit

      comment: this.info.comment,
      commentImgs: this.info.commentImgs,
      defaultAvatar,

      defaultIndex: 0,

      // at成员列表
      members: [],
    };
  },
  methods: {
    handleClickEdit() {
      this.mode = 'edit';
    },
    async handleClickDelete() {
      if (await this.$confirmBool(i18n.t('model.comment.confirm_delete'), i18n.t('public.dialog.prompt'), {
        confirmButtonText: i18n.t('public.dialog.real_confirm'),
        cancelButtonText: i18n.t('public.dialog.cancel'),
        closeOnClickModal: false,
        type: 'warning',
      })) {
        await DeleteComment([this.info.id]);
        this.$emit('on-delete-comment', this.info);
      }
    },
    // 用viewer查看大图
    handleViewPicture(index) {
      let targetImgs = this.info.contentImgs || this.info.imgs;
      targetImgs = targetImgs.map(e => {
        return {
          src: e.url,
        };
      });
      this.$ImageViewer.show(targetImgs, {
        index,
        targetElems: `[data-img-type='comment-item-${this._uid}']`,
      });
    },
    account2Username(comment, atableMembers, i) {
      const reg = /@([^ ]*) /g;
      return comment.replace(reg, ($0, account) => {
        let found;
        if (account === i.account) {
          found = i;
        } else {
          found = atableMembers.find(user => account === user.account);
        }
        return `<span class="user">${found && found.name && found.name.trim() !== '' ? `@${found.name}` : $0}</span>&nbsp;`;
      });
    },
  },
};
</script>

