<template>
  <div ref="pictures" class="comment-picture-editor-container">
    <el-popover trigger="click" placement="top"
      @visible-change="(v) => { active = v }"
      popper-class="comment-picture-popover">
      <div class="wrapper" :class="{ active }" slot="reference">
        <i class="iconfont icon-picture"></i>
        <span v-if="mode === 'add'">
          {{ $t('model.comment.add_image') }}
        </span>
        <span v-else-if="mode === 'edit'">
          {{ $t('model.comment.edit_image') }}
        </span>
      </div>
      <div>
        <div v-if="imgsList.length > 0" class="upload-tips"
          v-html="$t('model.comment.image_count_limit', {number: 9})">
        </div>
        <UploadImages @upload-img="l=>imgs=l" :pImgs="pImgs" />
        <!-- </div> -->
      </div>
    </el-popover>
  </div>
</template>

<script>
import UploadImages from './UploadImages';
// import 'viewerjs/dist/viewer.css';

export default {
  components: {
    UploadImages,
  },
  props: {
    mode: {
      type: String,
      default: 'add', // add or edit
    },
    pImgs: {
      type: Array,
    },
  },
  model: {
    prop: 'pImgs',
    event: 'updateImgs',
  },
  data() {
    return {
      active: false,
      imgs: [],
      imgsList: [],
    };
  },
  watch: {
    imgs(newList) {
      this.imgsList = newList.slice().map(each => each.thumb);
      this.$emit('updateImgs', newList);
    },
  },
};
</script>
<style>
.comment-picture-popover {
  min-width: 106px;
}
</style>

