<template>
  <div class="icon-groups" :class="{'is-disabled': buttonDisabled}">
    <!-- 工具栏右侧: 操作的前进和后退 -->
    <div class="flex">
      <a :data-no-permission="!$can('workspace', 'OP0014')">
        <el-tooltip :content="$t('model.detail.operation.undo')">
          <i class="iconfont iconredo_nol icon-btn" @click="!buttonDisabled && $emit('backward-operation')" />
        </el-tooltip>
      </a>
      <a :data-no-permission="!$can('workspace', 'OP0014')">
        <el-tooltip :content="$t('model.detail.operation.redo')">
          <i class="iconfont iconredo_nol backward icon-btn" @click="!buttonDisabled && $emit('forward-operation')" />
        </el-tooltip>
      </a>
      <hr>
      <a>
        <el-tooltip :content="$t('model.detail.operation.help_center')">
          <i class="iconfont iconhelp_nol icon-btn" @click="!buttonDisabled && $emit('open-instruction-dialog', 0)" />
        </el-tooltip>
      </a>

      <!-- <a :data-no-permission="!$can('workspace', 'OP0014')">
        <el-dropdown trigger="click" placement="bottom" @visible-change="pfsVisible = $event">
          <span>
            <el-tooltip :content="$t('model.detail.operation.settings')">
              <i class="iconfont iconHelp-nol1" />
            </el-tooltip>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="fps === 'Fast' ? fps = 'HD' : fps = 'Fast'">
              <span class="settings-dropdown-item">
                <i class="iconfont icon-pfs" />{{$t('model.detail.resolution')}}:
                {{ fps === 'Fast' ? $t('model.detail.hdResolution') : $t('model.detail.normal') }}
              </span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </a> -->

      <a>
        <el-tooltip :content="$t('model.detail.design_details')">
          <i class="iconfont iconDetails-nol icon-btn" @click="!buttonDisabled && $emit('open-dialog', 'modelDetail')" />
        </el-tooltip>
      </a>

      <a v-if="BrowserType !== 'Safari'">
        <el-tooltip :content="fullscreen?
          $t('model.detail.normal_screen') : $t('model.detail.full_screen')">
          <i class="iconfont iconfangda-nol1 icon-btn"
            @click="!buttonDisabled && showFullScreen()" />
        </el-tooltip>
      </a>

      <a>
        <el-tooltip :content="$t('model.detail.model_review')" ref='modelReviewTooltip' :tabindex="-1">
          <i class="iconfont i-moxingqueren icon-btn"
            @click="checkModel" />
        </el-tooltip>
      </a>
    </div>

    <!-- 工具栏中间: 扩展功能 -->
    <div class="flex">
      <a :data-no-permission="!$can('workspace', 'OP0054')">
        <el-tooltip :content="$t('model.detail.revision')">
          <i class="iconfont iconRevisions-nol icon-btn"
            :class="{active:$isActing(handleShowVersion)}"
            @click="!buttonDisabled && $act(handleShowVersion)" />
        </el-tooltip>
      </a>
      <a v-if="workspaceId" :data-no-permission="!$can('workspace', 'OP0052')">
        <el-tooltip :content="$t('model.detail.comments')">
          <el-badge :is-dot="hasDot && $can('workspace', 'OP0052')">
            <i class="iconfont iconComment-nol icon-btn"
              :class="{active:$isActing(handleShowComment)}"
              @click="!buttonDisabled && $act(handleShowComment)" />
          </el-badge>
        </el-tooltip>
      </a>
      <a v-if="workspaceId"
        :data-no-permission="!$can('workspace', 'OP0151')">
        <el-tooltip :content="$t('model.detail.3dTag')">
          <i class="iconfont iconpizhu_nol icon-btn"
            :class="{ active: modelDetail.annotationStatus }"
            @click="!buttonDisabled && $act(toggleAnnotationMode)" />
        </el-tooltip>
      </a>
      <a :data-no-permission="!$can('workspace', 'OP0155')">
        <el-tooltip :content="$t('model.detail.rendering')">
          <i class="iconfont icon-camera icon-btn"
            :class="{ active: $isActing(handleShowRenderSetting) }"
            @click="!buttonDisabled && $act(handleShowRenderSetting)" />
        </el-tooltip>
      </a>
      <a :data-no-permission="!$can('workspace', 'OP0157')">
        <el-tooltip :content="$t('model.detail.design_album')">
          <i class="iconfont iconxiangce_nol icon-btn"
            :class="{ active: modelDetail.photoStatus }"
            @click="!buttonDisabled && $act(togglePhotoStatus)" />
        </el-tooltip>
      </a>
    </div>

    <!-- 保存按钮组 -->
    <div class="flex" v-if="model">
      <span v-if="model" class="title text-one-line">{{model.modelNumber }}</span>
      <hr />
      <Save-button
        :disabled="buttonDisabled"
        :data-no-permission="!$can('workspace', 'OP0014')"
        :modelNumber="model.modelNumber"
        class="save-button" />
    </div>
    <div class="flex" v-else />
  </div>
</template>

<script>
import i18n from 'i18next';
import screenfull from 'screenfull';
import delay from '_/delay';
import axios from '@/api/axios';
import { openSingleton } from '@/utils/open-window';
import { queryHasDot } from '@/api/comment';
import Comment from '../comment';
import Version from '../version';
import RenderSetting from '../../offline/RenderSetting';
import SaveButton from './SaveButton';
import eventBus from './eventBus';


const apiStartRenderTask = (data, groupAppId) => {
  return axios({
    method: 'post',
    url: '/show/renderTask/start',
    headers: { groupAppId },
    data,
  });
};

export default {
  name: 'iconGroup',
  props: {
    // fullscreen: { type: Boolean, default: false },
    model: {},
    modelNumber: {},
  },
  components: {
    SaveButton,
  },
  data() {
    return {
      BrowserType: '',
      fps: 'HD', // 帧数的效果
      hasDot: false,
      scaleExpand: true,
      buttonDisabled: true,
      firstTimeUsingModelReview: true,
    };
  },
  computed: {
    /** @return { Vue | undefined } */
    modelDetail() {
      return this.$closest('model');
    },

    workspaceId() {
      return this.$route.query.workspaceId;
    },

    fullscreen() {
      return this.$root.isFullscreen;
    },
  },
  mounted() {
    // 判断url是否包含commentDialogOpen信息，包含则打开评论弹框
    if ((this.$route.query.commentDialogOpen)) {
      this.handleShowComment();
      // 去掉url中的打开参数
      this.$router.replace(this.$route.fullPath.slice(0, this.$route.fullPath.indexOf('&commentDialogOpen')));
    }
    this.judgeHasDotOrNot();

    this.BrowserType = this.detectBrowserType();

    eventBus.$on('renderer-loaded', () => {
      this.buttonDisabled = false;
    });
  },
  methods: {
    // 模型确认
    checkModel() {
      const targetVue = this.$refs.modelReviewTooltip;
      const { referenceElm } = targetVue;
      const blurPopper = async () => {
        await this.$nextTick();
        targetVue.handleBlur();
        // referenceElm.removeEventListener('focus', blurPopper, true);
      };
      if (this.firstTimeUsingModelReview) {
        referenceElm.addEventListener('focus', blurPopper, true);
        this.firstTimeUsingModelReview = false;
      }

      openSingleton({
        name: 'ModelReview',
        params: {
          modelId: this.$attrs.modelId,
        },
        query: {
          workspaceId: this.$attrs.workspaceId,
        },
      }, true, 'ModelReview');
    },
    // 显示批注模式
    toggleAnnotationMode() {
      this.modelDetail.annotationStatus = !this.modelDetail.annotationStatus;
      this.$emit('creating-annotation');
      if (this.modelDetail.annotationStatus) this.modelDetail.photoStatus = false;
    },
    // 切换渲染图册中的图标状态
    togglePhotoStatus() {
      this.modelDetail.photoStatus = !this.modelDetail.photoStatus;
      if (this.modelDetail.photoStatus) this.modelDetail.annotationStatus = false;
    },
    async handleShowComment() {
      this.hasDot = false;
      console.log(Comment);
      // debugger;
      await this.$Dialog.dialogify({
        is: Comment,
        key: 'comment',
      }, {
        key: 'comment',
      });
    },
    // 显示历史版本弹框
    async handleShowVersion() {
      const targetVersion = await this.$Dialog.dialogify({
        is: Version,
      });
      const reg = /(.+model\/)(\d+)(.+)/;
      const newFullPath = this.$route.fullPath.replace(reg, `$1${targetVersion.relateId}$3`);
      this.$router.replace(newFullPath);
    },
    // 显示渲染图册配置
    async handleShowRenderSetting() {
      const renderer = this.$parent;
      const modifiedRecord = renderer.d4 ? renderer.d4.getRecord() : null;

      if (renderer && modifiedRecord) {
        const newCameraJson = JSON.parse(renderer.d4.outputCameraJson());
        const oldCameraJson = JSON.parse(renderer.envSetting.cameraJson);
        if (newCameraJson.projection !== oldCameraJson.projection) {
          // console.log('modifiedRecord========', modifiedRecord);
          modifiedRecord.push({
            component: 'environmentSetting',
            newValue: {
              cameraMode: newCameraJson.projection,
            },
            oldValue: {
              cameraMode: oldCameraJson.projection,
            },
            property: 'environmentSetting',
          });
        }
      }

      // 如果有修改记录，则先保存
      if (modifiedRecord && modifiedRecord.length > 0) {
        await this.$confirm(i18n.t('model.offline.save_before_render_tip'), i18n.t('model.offline.save_before_render_title'), {
          confirmButtonText: i18n.t('public.dialog.save'),
          cancelButtonText: i18n.t('model.offline.cancel_render'),
        });
        try {
          await this.$parent.save('save');

          // 等待渲染页模型加载完成再打开弹窗
          await new Promise(resolve => {
            // 设置10秒超时，若15秒仍未加载完成，继续执行弹窗，不阻塞
            const timer = setTimeout(() => {
              eventBus.$off('renderer-loaded');
              resolve();
            }, 10000);
            eventBus.$once('renderer-loaded', () => {
              clearTimeout(timer);
              resolve();
            });
          });
        } catch (err) {
          // this.$errHandler(err);
        }
      }
      const result = await this.$Dialog.dialogify({
        is: RenderSetting,
        props: {
          cameraType: JSON.parse(this.$closest('model').renderer.envSetting.cameraJson).projection,
        },
      }, {
        key: (new Date()).valueOf(),
      });
      if (result) {
        console.log(Object.assign(result, { shoeId: this.$route.params.modelId }));
        await apiStartRenderTask(
          Object.assign(result, { shoeId: this.$route.params.modelId }),
          this.workspaceId,
        );
        this.$closest('model').photoStatus = true;
        this.$message.success(i18n.t('model.detail.render_processing'));
      }
    },
    async judgeHasDotOrNot() {
      if (!this.workspaceId) return;
      // 判断是否需要显示小红点
      const { data } = await queryHasDot({
        objectId: this.$route.query.catalogId,
      });
      this.hasDot = data;
    },
    detectBrowserType() {
      const { userAgent } = navigator; // 取得浏览器的userAgent字符串
      const isOpera = userAgent.indexOf('Opera') > -1;
      if (isOpera) {
        return 'Opera';
      }
      // 判断是否Opera浏览器
      if (userAgent.indexOf('Firefox') > -1) {
        return 'FF';
      } // 判断是否Firefox浏览器
      if (userAgent.indexOf('Chrome') > -1) {
        return 'Chrome';
      }
      if (userAgent.indexOf('Safari') > -1) {
        return 'Safari';
      } // 判断是否Safari浏览器
      if (userAgent.indexOf('compatible') > -1 && userAgent.indexOf('MSIE') > -1 && !isOpera) {
        return 'IE';
      }
      // 判断是否IE浏览器
      return 'Unknow';
    },
    showFullScreen() {
      if (!screenfull.enabled) {
        this.$message.warning(i18n.t('model.detail.browser_not_support_tip'));
      } else {
        this.$root.toggleFullscreen();
        delay(100).then(() => {
          // this.resize();
          this.$emit('resize');
        });
      }
    },
  },
};
</script>

