<template>
  <div class="save-as-dialog-wrapper">
    <el-form class="save-as-confirm-content" :rules="rules" label-width="100px"
      label-position="left" :model="form" ref="form">
      <el-form-item :label="$t('model.detail.2d.file_name') + ':'" prop="modelNumber">
        <el-input v-model="form.modelNumber" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item :label="$t('model.detail.2d.file_location') + ':'">
        <div class="path-wrapper">
          {{ path }}
        </div>
      </el-form-item>
    </el-form>
    <div class="flex-justify-flex-end button-group">
      <el-button class="button-cancel"
        round
        @click="$emit('cancel')"
        style="margin-right: 10px;">
        {{ $t('public.dialog.cancel') }}
      </el-button>
      <el-button round type="primary" @click="handleSave">
        {{ $t('model.detail.2d.save') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import validator, { validateFormFields } from '@/utils/validators/rules-validation';
import {
  getCatalogUrl,
} from '@/api/catalogue';

export default {
  dialogify: {
    props: {
      title: i18n.t('model.detail.2d.save_as'),
      // titleAlign: 'left',
      width: '440px',
    },
  },
  props: ['modelNumber'],
  data() {
    return {
      path: '',
      form: {
        modelNumber: this.modelNumber,
      },
      rules: {
        modelNumber: validator.rules('requirement'),
      },
    };
  },
  methods: {
    async handleSave() {
      try {
        await validateFormFields(this.$refs.form);
        this.$emit('confirm', this.form.modelNumber);
      } catch (e) {
        throw this.$errHandler(e);
      }
    },
  },
  async mounted() {
    const { catalogId } = this.$route.query;
    let { data: pathList } = await getCatalogUrl(catalogId);
    pathList = pathList.sort((a, b) => b.sort - a.sort);
    // eslint-disable-next-line
    pathList[0].name = pathList[0].name === 'SHOE' ? 'My Designs'
      // eslint-disable-next-line
      : (pathList[0].name === 'MATERIAL' ? 'My Materials' : (
        pathList[0].name === 'WORKSPACE' ? 'Workspaces' : ''
      ));
    pathList.pop();
    this.path = pathList.reduce((a, b) => {
      a = `${a}${b.name} > `;
      return a;
    }, '');
    this.path = this.path.slice(0, this.path.length - 2);
    console.log(this.path);
  },
};
</script>

<style lang="scss" scoped>
.save-as-dialog-wrapper {
  /deep/ .el-form-item {
    margin-bottom: 5px;
  }
  .button-group {
    margin-top: 20px;
  }
  .path-wrapper {
    padding-top: 8px;
    line-height: 25px;
    word-break: normal;
  }
}
</style>
