<template lang="html">
<el-popover
  width="200"
  placement="left-start"
  :visible-arrow="false"
  :close-delay="0"
  :disabled="!material.texture && !material.flat"
  transition=""
  v-model="textureVisible"
  trigger="manual">
  <BaseImage width="200" height="200" fit="contain" :src="material.texture || material.flat">
    <div class="img-loading" slot="placeholder" >
      <i class="el-icon-loading"></i>
    </div>
  </BaseImage>
  <BaseImage fit="contain" :src="require('@/assets/img/material/scale-horizontal.png')" v-if="material.texture"></BaseImage>
  <div class="text-one-line">{{ material.name }}</div>
  <div slot="reference">
    <figure class="material-item"
      ref="materialItem"
      :class="{
        selected: selected,
        copying: isCopying,
        moving: isMoving
      }"
      tabindex="0"
      @click.stop="pick"
      @contextmenu.stop="handleContextmenu"
      @dragstart="handleDragStartMaterial(material, $event)"
      @dragover.prevent
      :draggable="!unavailableResaon">

      <BaseImage :lazy="true"
        data-img-type='material-list'
        :data-src="imgSrc"
        :style="{
            width: '96px',
            height: '96px',
          }"
        :src="imgSrc" />
      <!-- <figcaption class="material-item-name">{{ material.name }}</figcaption> -->

      <!-- 小功能按钮 -->
      <i class="iconfont icon-help"
        @click.stop="$emit('show-info', material)" />
      <i
        class="iconfont icon-tick-circle"
        v-if="selectable"
        @click.stop="$emit('update:selected', !selected)"
      />
      <i class="iconfont icon-zoom" @click.stop="$emit('magnify', displayMode)" />
      <i class="iconfont icon-heart" @click.stop="handleUnlike" v-if="material.formFavorite" />
      <span v-if="unavailableResaon" class="material-item-publishstatus">
        {{unavailableResaon}}
      </span>
      <small v-if="costPrice" class="material-item-price">{{costPrice}}</small>
    </figure>
    <el-tooltip class="item" effect="dark" placement="bottom" popper-class="material-card-name-tooltip">
      <div slot="content" class="text-one-line">
        <div>{{ material.name }}</div>
        <div v-if="材料规格">{{ 材料规格 }}</div>
      </div>
      <span class="material-item-name text-one-line">
        {{ material.name }}
      </span>
    </el-tooltip>
    <div class="row" v-if="partUsed">
      <span
        class="text-one-line"
        :title="partUsed">{{ partUsed }}
      </span>
    </div>
  </div>
  </el-popover>
</template>

<script>

import BaseImage from '@/components/base/Image';
import clipboard from '@/utils/clipboard';
import { axios } from '@/utils/axios';
import i18n from 'i18next';
import MaterialStatus from '@/models/MaterialStatus';

export default {
  components: {
    BaseImage,
  },
  props: {
    selected: {
      type: Boolean,
    },
    material: {
      type: Object,
      required: true,
    },
    // 面料的现实模式 flat/drape
    displayMode: {
      type: String,
      default: 'drape',
    },
  },
  data() {
    return {
      textureVisible: false,
    };
  },
  computed: {
    材料规格() {
      return this.material.extraProperty?.材料规格;
    },
    // 使用部位
    partUsed() {
      return this.material.part_used || this.material.partUsed || '';
    },
    // 成本单价
    costPrice() {
      const { material: _material } = this;
      const supplierPriceObj = _material.supplier_price_obj ?? {};
      const unit2symbol = {
        1: '￥',
        2: '$',
      };
      let v;
      let u;
      if (supplierPriceObj.cost_value) {
        v = supplierPriceObj.cost_value;
        u = supplierPriceObj.cost_unit;
      } else if (supplierPriceObj.reference_price_value) {
        v = supplierPriceObj.reference_price_value;
        u = supplierPriceObj.reference_price_unit;
      } else {
        v = _material.samplePrice;
        u = _material.samplePriceUnit;
      }
      return u && v ? `${unit2symbol[u]}${v}` : '';
    },

    selectable() {
      return this.$listeners['update:selected'];
    },
    imgSrc() {
      return (this.displayMode === 'flat'
        ? this.material.texture ?? this.material.flat : this.material.thumbnail) || null;
    },
    inClipboard() {
      return clipboard.type === 'catalog'
        && clipboard.dataArr.some(catalog => catalog.fileInfo === this.material);
    },
    isCopying() {
      return clipboard.command === 'copy'
        && this.inClipboard;
    },
    isMoving() {
      return clipboard.command === 'move'
        && this.inClipboard;
    },
    unavailableResaon() {
      const { material } = this;
      if (material.deleteStatus) {
        return i18n.t('model.detail.board.deleted');
      } if (material.status === MaterialStatus.INACTIVE) {
        return i18n.t('model.detail.board.inactive');
        // } if (material.status === MaterialStatus.REJECTED) {
        //   return i18n.t('model.detail.board.rejected');
        // } if (material.status === MaterialStatus.DEVELOPING) {
        //   return i18n.t('model.detail.board.developing');
      } if (material.publishStatus === 3) {
        return i18n.t('model.detail.board.unpublished');
      }
      return null;
    },
  },
  methods: {
    handleContextmenu(e) {
      if (this.$listeners.contextmenu) {
        e.preventDefault();
        this.$emit('contextmenu', e, this.material);
      }
    },
    handleDragStartMaterial(data, e) {
      this.$emit('drag', data, e);
    },
    handleShowTexture() {
      this.textureVisible = true;
    },
    handleHideTexture() {
      this.textureVisible = false;
    },
    async handleUnlike() {
      const confirm = await this.$confirmBool('确定取消收藏吗？');
      if (confirm) {
        await axios.post('/mall/collect/remove', {
          id: this.material.goodId,
          materialId: this.material.id,
        });
        this.$closest('LibraryList').t = Date.now();
      }
    },
    pick() {
      this.$emit('pick', this.material);
    },
  },
  mounted() {
    this.$refs.materialItem.addEventListener('mouseenter', this.handleShowTexture);
    this.$refs.materialItem.addEventListener('mouseleave', this.handleHideTexture);
  },
  unmounted() {
    this.$refs.materialItem.removeEventListener('mouseenter', this.handleShowTexture);
    this.$refs.materialItem.removeEventListener('mouseleave', this.handleHideTexture);
  },
};
</script>

