<template>
  <section class="comment-add-container">
    <at-ta :members="membersWithAll" name-key="account">
      <template v-slot:item="{item}">
        <!-- <span> -->
        <!-- <span> -->
        <BaseImage class="user-avatar"
          :src="item.avatar || item.headImg || defaultAvatar" fit="cover" />
        <el-tooltip :disabled="!item.department"
          :content="item.name || item.account" placement="right">
          <span>{{ item.name || item.account }}</span>
        </el-tooltip>
        <span v-if="item.department">({{ item.department }})</span>
        <!-- </span> -->
        <!-- </span> -->
      </template>
      <el-input type="textarea" v-model.lazy="comment"
        :placeholder="$t('model.comment.input_placeholder')"></el-input>
    </at-ta>

    <div class="comment-footer">
      <!-- 图片编辑器 -->
      <CommentPictureEditor v-model="commentImgs" :mode="mode === 'add' ? 'add' : 'edit'" />

      <div class="button-group" v-if="mode === 'add'">
        <el-button type="primary" @click="handlePublishComment">
          {{ $t('model.comment.publish') }}
        </el-button>
      </div>
      <div class="button-group" v-else-if="mode === 'edit'">
        <el-button @click="mode='view';$emit('on-edit-comment');">
          {{ $t('public.dialog.cancel') }}
        </el-button>
        <el-button type="primary" @click="handleEditComment">
          {{ $t('public.dialog.confirm') }}
        </el-button>
      </div>
    </div>
  </section>
</template>

<script>
// import At from 'vue-at';
import AtTa from 'vue-at/dist/vue-at-textarea';
import i18n from 'i18next';
import BaseImage from '@/components/base/Image.vue';
import { getModelInfo, getVersionDetail } from '@/api/shoe';
import defaultAvatar from '@/assets/img/header/default_avatar.png';

import {
  AddComment,
  EditComment,
} from '@/api/comment';
import CommentPictureEditor from './CommentPictureEditor';


export default {
  name: 'CommentAdd',
  props: {
    mode: {
      type: String,
      default: 'add',
    },
    // 编辑模式需要的参数
    pComment: String,
    pCommentImgs: Array,
    id: String,
    members: Array,
    modelId: {
      default() {
        return this.$route.params.modelId;
      },
    },
    catalogId: {
      default() {
        return this.$route.query.catalogId;
      },
    },
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  components: {
    BaseImage,
    CommentPictureEditor,
    AtTa,
  },
  data() {
    return {
      comment: '',
      commentImgs: [],
      version: {},
      defaultAvatar,

      // 发送信息时需要的params，包括catalogId和shoename
      params: {},
    };
  },
  computed: {
    membersWithAll() {
      const atAll = {
        account: i18n.t('model.comment.all'),
        headImg: require('@/assets/img/renderer/at-all.jpg'),
        id: 'all',
        department: '',
        name: '',
      };
      if (!this.members) {
        return [atAll];
      }
      return [atAll, ...this.members];
    },
  },
  methods: {
    async handlePublishComment() {
      // 获取at的人的id列表
      const at = this.parsingAndFindAtMembers().map(e => e.id);
      try {
        this.validate();
        await AddComment(this.workspaceId, {
          catalogId: this.catalogId,
          imgs: this.commentImgs.map(e => { return { path: e.path }; }),
          content: this.comment,
          at,
          params: this.params,
          versionName: this.version.name || 'no name',
          versionId: this.version.id,
        });
        this.clearData();
        this.$emit('on-add-comment');
      } catch (e) {
        this.$message.error(e.message);
      }
    },
    // 点击确定编辑评论
    async handleEditComment() {
      const at = this.parsingAndFindAtMembers().map(e => e.id);
      try {
        this.validate();
        await EditComment(this.workspaceId, {
          id: this.id,
          imgs: this.commentImgs.map(e => { return { path: e.path }; }),
          content: this.comment,
          params: this.params,
          at,
        });
        this.$emit('on-edit-comment');
      } catch (e) {
        this.$message.error(e.message);
      }
    },
    // 全局匹配(@名字 )并在列表中找到该对象，放在一个数组中
    parsingAndFindAtMembers() {
      const reg = /@([^ ]*) /g;
      const members = [];
      this.comment.replace(reg, ($0, account) => {
        const found = this.membersWithAll.find(user => account === user.account);
        if (found) {
          members.push(found);
        }
      });
      return members;
    },
    // 提交前的校验
    validate() {
      if (this.comment.trim() === '' && this.commentImgs.length === 0) {
        throw new Error(i18n.t('model.comment.empty_tip'));
      }
      if (this.commentImgs.some((item) => item.percentage !== 100)) {
        throw new Error('Images have not been uploaded yet, please wait...');
      }
    },
    // 清空数据
    clearData() {
      this.comment = '';
      this.commentImgs = [];
    },
    // 获取@人时需要提供的信息，目前包括catalogId、shoeName
    async getParams() {
      const params = {};
      params.catalogId = this.catalogId;
      params.workspaceId = this.workspaceId;
      const { data, code } = await getModelInfo(this.modelId);
      if (code === 200) {
        params.shoeName = data.modelNumber;
      }
      this.params = params;
    },
  },
  watch: {
    comment(newVal) {
      if (!newVal) return;
      const peek = newVal[newVal.length - 1];
      if (peek === '@') {
        // 当前面木有空格时，加个空格
        if (newVal.length === 1 || newVal[newVal.length - 2] === ' ') return;
        this.comment = this.comment.slice(0, -1);
        this.comment += ' @';
      }
    },
  },
  mounted() {
    getVersionDetail(this.catalogId).then(({ data }) => { this.version = data; });
    if (this.mode === 'edit') {
      this.comment = this.pComment || '';
      if (!this.pCommentImgs) return;
      this.commentImgs = this.pCommentImgs.slice().map(each => {
        each.thumb = each.url;
        each.percentage = 100;
        return each;
      });
    }

    this.getParams();
  },
};
</script>

<style lang="scss" scoped>
.comment-add-container {
  margin-top: 1.5rem;
}
.comment-footer {
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/deep/ .el-textarea,
/deep/ .el-textarea__inner {
  background-color: #eee;
}
.user-avatar {
  flex: none;
  margin-right: 8px;
  height: 24px;
  width: 24px;
}
</style>
