<template>
  <div class="current-list">
    <CollectList
      class="material-list"
      :materials="materials"
      :isCurrentDesign="true"
      :displayMode.sync="displayMode"
      @drag="handleMaterialDrag"
      @pick="handleMaterialPick" />
  </div>
</template>

<script>
import draggingData from '../../../../utils/dragging-data';
import CollectList from './CollectList.vue';

export default {
  name: 'currentDesign',
  components: {
    CollectList,
  },
  data() {
    return {
      displayMode: 'drape',
    };
  },
  computed: {
    /** @returns { object[] } */
    materials() {
      return this.$closest('model').renderer.currentMaterials;
    },
  },
  methods: {
    handleMaterialDrag(e, data) {
      draggingData.set({ type: 'material', data });
    },
    handleMaterialPick(data) {
      this.$closest('model').updateMaterial(data);
    },
    handleMouseDown(event) {
      const startY = event.clientY;
      const listHeight = parseInt(getComputedStyle(this.$refs.list.$el).height, 10);
      const handleDrag = (e) => {
        const deltaY = startY - e.clientY;
        this.$refs.list.$el.style.height = `${deltaY + listHeight}px`;
      };
      document.addEventListener('mousemove', handleDrag);
      const handleDragEnd = () => {
        document.removeEventListener('mousemove', handleDrag);
        document.removeEventListener('mouseup', handleDragEnd);
      };
      document.addEventListener('mouseup', handleDragEnd);
    },
  },
};
</script>

<style lang="scss" scoped>
.current-list {
  display: flex;
  flex-direction: column;
  flex: 1;

  .material-list {
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
  }
  .middle-part {
    padding: 8px 0;
    cursor: row-resize;
    &::before {
      content: "";
      display: block;
      height: 2px;
      background-color: #f4f7fc;
    }
  }
}
</style>
