/* eslint-disable class-methods-use-this */

import {
  Tool,
  PaperScope as _PaperScope,
  Path as _Path,
  KeyEvent,
} from 'paper';

type PagerScope = InstanceType<typeof _PaperScope>;
type Path = InstanceType<typeof _Path>;
type ToolType = InstanceType<typeof Tool>;
type KeyEventType = InstanceType<typeof KeyEvent>;


export class BaseTool extends Tool {
  static history: string[] = [];
  static historyIndex: number = -1;
  pushHistory() {
    const json = this.project.exportJSON();
    const { history, historyIndex: index } = BaseTool;
    if (json !== history[index]) {
      const iAnd1 = index + 1;
      history.splice(iAnd1, history.length - iAnd1, json);
      BaseTool.historyIndex = iAnd1;
    }
  }
  undo(step = 1) {
    const index = BaseTool.historyIndex - step;
    if (index >= -1) {
      BaseTool.historyIndex = index;
      this.project.clear();
      if (index !== -1) {
        this.project.importJSON(BaseTool.history[index]);
      }
    }
  }
  clearHistory() {
    BaseTool.history.splice(0, BaseTool.history.length);
    BaseTool.historyIndex = -1;
  }
  get path() {
    const { Path } = this.scope;
    return (this.layer.getItem({ class: Path, selected: true }) as Path | undefined) ?? null;
  }
  constructor(public scope: PagerScope) {
    super();
    this.on('mousedown', () => {
      const off = () => {
        setTimeout(() => {
          this.pushHistory();
        }, 0);
        this.off('mouseup', off);
      };
      this.on('mouseup', off);
    });
    this.on('keydown', () => {
      const off = () => {
        setTimeout(() => {
          this.pushHistory();
        }, 0);
        this.off('keyup', off);
      };
      this.on('keyup', off);
    });
    this.on('keyup', (e: KeyEventType) => {
      if ((e.modifiers.control || e.modifiers.command) && e.key === 'z') {
        this.undo();
      }
    });
  }

  get project() {
    return this.scope.project;
  }
  get layer() {
    return this.project.activeLayer;
  }
}

export default BaseTool;
