<template>
  <div class="material-list">
    <header class="header">
      <div class="brief">
        <slot name="brief"></slot>
      </div>
      <div class="view-tools">
        <DisplayModeSwitcher :value="displayMode" @input="v => $emit('update:displayMode', v)" />
      </div>
    </header>
    <div class="loading" v-loading="true" v-if="materials === $loadingState"></div>
    <div class="empty" v-else-if="!materials || !materials.length">
      <slot name="empty" :keyword="keyword" :materials="materials"></slot>
    </div>

    <Scrollable y v-else class="list-wrapper" outsideScrollbar>
      <transition-group name="list" tag="ul" class="list" @click.stop>
        <li v-for="(material, idx) in materials" :key="material.id || material.originalId" class="item">
          <material-card
            @pick="material => $emit('pick', material)"
            @drag="handleItemDrag"
            :display-mode="displayMode"
            :material="material"
            @show-info="handleShoWDialogInfo"
            @magnify="showMagnifyMode(idx)"
          />
        </li>
      </transition-group>
    </Scrollable>
  </div>
</template>

<script>
import { openSingleton } from '@/utils/open-window';

import Scrollable from '@/components/Scrollable';
import MaterialInfo from '@/views/material/info';
import AntaMaterialInfo from '@/views/material/info-anta/index.vue';
import toggleFrom from '_/toggle-from';
import DisplayModeSwitcher from './DisplayModeSwitcher';
import MaterialCard from './MaterialCard';
// import { getMaterialInfo } from '@/api/material';


export default {
  name: 'CollectList',
  components: {
    MaterialCard,
    Scrollable,
    DisplayModeSwitcher,
  },
  props: {
    keyword: String,
    /** @type { any[] } */
    materials: {
      type: Array,
      required: true,
    },
    displayMode: {
      type: String,
      default: 'drape',
    },
  },
  methods: {
    toggleSelected(material, selected) {
      toggleFrom(this.selected, material, selected);
    },
    handleItemDrag(material, e) {
      if (material.publishStatus === 3) {
        e.preventDefault();
        return;
      }
      this.$emit('drag', e, [material]);
    },
    // 显示面料信息弹框，将当前面料对象存储
    async handleShoWDialogInfo(material) {
      try {
        const Board = this.$closest('board');
        if (material.goodId) {
          openSingleton({
            path: '/material-mall/goods-detail',
            query: {
              id: material.goodId,
              materialId: material.id,
              fromLibrary: true,
            },
          }, true, 'GoodsDetail');
        } else {
          const businessType = material.businessType ?? 0;

          await this.$Dialog.dialogify({
            is: businessType === 1 ? AntaMaterialInfo : MaterialInfo,
            props: {
              id: material.id,
              groupAppId: Board.groupAppId,
              // 渲染页，不允许编辑面料详情
              editable: false,
            },
          }, {
            props: {
              top: 'auto',
              bottom: 'auto',
            },
          });
        }
      } catch (err) {
        this.$errHandler(err);
      }
    },

    // 显示放大镜效果，通过点击小按钮触发
    showMagnifyMode(index) {
      const target = this.displayMode === 'drape' ? 'contentThumbnail' : 'flat';
      const srcList = this.materials.map(e => {
        return {
          src: e[target] || e.thumbnail,
        };
      });

      this.$ImageViewer.show(srcList, {
        index,
        targetElems: '[data-img-type=\'material-list\']',
      });
    },
  },
};
</script>

